import { RIconButton } from "components/Buttons/RIconButton"
import { RText } from "components/Typography/RText"
import { RTextBold } from "components/Typography/RTextBold"
import { StyleSheet, View } from "react-native"
import { ContactType } from "types/shared"


interface Props {
    type: ContactType,
    preferredContact: ContactType
    onPress: (type: ContactType) => void
}

export const ContactNumber = (props: Props) => {
    const label = props.type.toString() + " Number:"
    const isSelected = props.preferredContact === props.type;

    const icon = isSelected ? "star" : "star-outline"

    const onPress = () => {
        props.onPress(props.type)
    }

    return (
        <View>
            <RTextBold>{label}</RTextBold>
            <View style={styles.row}>
                <View style={{ flex: 1, justifyContent: 'center' }} >
                    <RText>0555 555 555</RText>

                </View>
                <View style={{ flex: 2, alignItems: 'center' }} >
                    <RIconButton onPress={onPress} icon={icon} />

                </View>

            </View>
        </View>


    )
}

export const styles = StyleSheet.create({
    container: {
        backgroundColor: 'white',
        flex: 1
    },
    row: {
        flexDirection: 'row',
        marginRight: 20
    }
})