import { LineSeperator } from "components/LIneSeperator"
import { Spacer } from "components/Spacer"
import { RText } from "components/Typography/RText"
import React from "react"
import { StyleSheet, TouchableOpacity, View } from "react-native"
import { ScreenName } from "types/navigation"
import { OverviewData } from "types/overview"
import { dateUtil } from "util/dateUtil"
import { navigationUtil } from "util/navigationUtil"


interface Props {
    data: OverviewData
}
export const OverviewLine = (props: Props) => {

    const data = props.data;

    const onPress = () => {
        navigationUtil.navigate(ScreenName.Jobcard)
    }

    const inDate = dateUtil.toFormDisplay(data.inDate, "DD/MM/YYYY HH:mm");
    const outDate = dateUtil.toFormDisplay(data.outDate, "DD/MM/YYYY HH:mm");

    return (<TouchableOpacity onPress={onPress}>
        <View style={[styles.container, styles.row]}>
            <View style={styles.firstColumn}>
                <View style={[styles.innerRow]}>
                    <View style={styles.innerColumn}>
                        <RText>{data.registration}</RText>
                    </View>
                    <View style={styles.innerColumn}>
                        <RText>{data.status}</RText>
                    </View>
                </View>
                <Spacer small />
                <RText containerStyle={styles.paddingRight}>{data.description}</RText>
            </View>
            <View style={styles.secondColumn}>
                <RText>{data.subStatus}</RText>
                <Spacer small />
                <RText>{data.contactName}</RText>
            </View>
            <View style={styles.rightColumn}>
                <RText>{inDate}</RText>
                <Spacer small />
                <RText>{data.contactNumber}</RText>
            </View>
            <View style={styles.rightColumn}>
                <RText>{outDate}</RText>
                <Spacer small />
            </View>
        </View>
        <LineSeperator />
    </TouchableOpacity >

    )
}

const styles = StyleSheet.create({
    container: {

        paddingVertical: 10,
        borderRadius: 4
    },
    row: {
        flexDirection: 'row'
    },
    firstColumn: {
        flex: 2,
    },
    secondColumn: {
        flex: 2,
    },
    rightColumn: {
        flex: 1,
    },
    innerColumn: {
        flex: 1
    },
    innerRow: {
        flexDirection: 'row'
    },
    paddingRight: {
        paddingRight: 20
    }


})