export const OverviewScreen = () => {

    const [sortedApiData, setSortedApiData] = useState(apiData)

    useEffect(() => {

    }, [])

    const onToggleSort = (label: string, sortDirection: SortDirection) => {
        if (label === 'Registration') {
            const sorted = overViewUtil.sortByRego(apiData, sortDirection);
            setSortedApiData([...sorted]);
        } else if (label === 'In Date') {
            const sorted = overViewUtil.sortByInDate(apiData, sortDirection);
            setSortedApiData([...sorted]);
        }
        else if (label === 'Out Date') {
            const sorted = overViewUtil.sortByOutDate(apiData, sortDirection);
            setSortedApiData([...sorted]);
        }

    }

    if (!sortedApiData) {
        return null;
    }

    return (
        <RScreenContainer title="Overview" noPadding  >
            <RCard flex>
                <AdvisorSection />

                <OverviewHeader onToggleSort={onToggleSort} />
                {sortedApiData.map((item, index) => {
                    return <OverviewLine data={item} key={index.toString()} />
                })}




            </RCard>



        </RScreenContainer>
    );
}
