import * as SplashScreen from 'expo-splash-screen';
import { splashActions } from 'reducers/splashReducer';
import { store } from 'reducers/store';


function hideExpoSplash() {
    SplashScreen.hideAsync();

}

function hideFakeSplash() {
    store.dispatch(splashActions.setIsVisible(false))
}

async function preventAutoHide() {
    await SplashScreen.preventAutoHideAsync();
}

export const splashUtil = {
    hideExpoSplash: hideExpoSplash,
    hideFakeSplash: hideFakeSplash,
    preventAutoHide: preventAutoHide
}