import AsyncStorage from '@react-native-async-storage/async-storage';
import { SaveableObject } from "types/SaveableObject";

let saveableObject = new SaveableObject();
const saveKeys = {
    general: "@general"
}

function canAutoLogin() {
    return false;
}

function getSavedState() {
    return saveableObject;
}

function saveQrData(data: any) {
    saveableObject.qrData = data;
    save();
}

function save() {
    const saveString = JSON.stringify(saveableObject);
    AsyncStorage.setItem(saveKeys.general, saveString)

}

function saveLoginInfo(licenceKey: string, password: string, url: string, username: string) {
    saveableObject.licenceKey = licenceKey;
    saveableObject.password = password;
    saveableObject.url = url;
    saveableObject.username = username;
    save();
}

function setDepartmentParameters(companyId: string, divisionId: string, departmentId: string) {
    saveableObject.companyID = companyId;
    saveableObject.divisionID = divisionId;
    saveableObject.departmentID = departmentId;
    save();
}

function setDeviceId(deviceId: string) {
    saveableObject.deviceId = deviceId;
    save();
}

async function loadSavedObject() {
    const jsonString = await AsyncStorage.getItem(saveKeys.general);
    saveableObject = { ...saveableObject, ...JSON.parse(jsonString) }
    console.log("loadSavedObject", saveableObject)
    return saveableObject;
}




export const saveUtil = {
    canAutoLogin: canAutoLogin,
    getSavedState: getSavedState,
    saveQrData: saveQrData,
    saveLoginInfo: saveLoginInfo,
    setDepartmentParameters: setDepartmentParameters,
    setDeviceId: setDeviceId,
    loadSavedObject: loadSavedObject
}