import { fontSizes } from "constants/fontSizes";
import React from "react";
import { StyleSheet, Text, View } from "react-native";

interface Props {
    children: any,
    small?: boolean,
    containerStyle?: any
    color?: string,
    systemFont?: boolean,
    underline?: boolean
}

export const RText = (props: Props) => {
    const fontSize = props.small ? fontSizes.small : fontSizes.normal
    const color = (props.color ? props.color : 'black')
    const fontFamily = props.systemFont ? undefined : 'RobotoCondensed-Regular'
    const underline = props.underline ? 'underline' : undefined
    const textStyle = [styles.text, { textDecorationLine: underline, fontSize: fontSize, color: color, fontFamily: fontFamily }]

    return (
        <View style={{ ...styles.container, ...props.containerStyle }}>
            {/*//@ts-ignore*/}
            <Text style={textStyle}  >{props.children}</Text>
        </View>
    )

}
const styles = StyleSheet.create({
    container: {
        flexShrink: 1 // needed to wrap text
    },
    text: {
        fontSize: fontSizes.normal,

    }
})