import { createMaterialTopTabNavigator } from "@react-navigation/material-top-tabs";
import { tabConfig } from "constants/tabConfig";
import React from "react";
import { View } from "react-native";
import { ContactTab } from "./ContactTab";
import { StarContactTab } from "./StarContactTab";

const Tab = createMaterialTopTabNavigator();


export const ContactSection = () => {



    return (
        <View style={{ height: 270 }}>
            <Tab.Navigator screenOptions={tabConfig.options}>
                <Tab.Screen name="Jobcard Contact" component={StarContactTab} initialParams={{ data: null }} />
                <Tab.Screen name="Driver" component={ContactTab} initialParams={{ data: null }} />
                <Tab.Screen name="Service Contact" component={ContactTab} initialParams={{ data: null }} />
                <Tab.Screen name="Owner" component={ContactTab} initialParams={{ data: null }} />
                <Tab.Screen name="Alt 1" component={ContactTab} initialParams={{ data: null }} />
                <Tab.Screen name="Alt 2" component={ContactTab} initialParams={{ data: null }} />

            </Tab.Navigator>
        </View>

    );
}
