import moment from "moment";
import { DateFormat } from "types/date";

function toMonthYearString(date: Date, defaultString: string) {
    if (!date) {
        return defaultString
    }
    try {
        const m = moment(date);
        return m.format("MM/YYYY")
    } catch {
        return defaultString
    }

}

function toReadableTimeString(apiString: string | Date) {
    return (moment(apiString).format('LT'));
}

function parseMonthYearString(dateString: string) {
    if (!dateString) {
        return null;
    }
    try {
        const fixedDateString = "01/" + dateString;
        const pattern = "DD/MM/YYYY"
        var m = moment(fixedDateString, pattern)
        const result = m.toDate();
        if (isValidDate(result)) {
            return result;
        }

        return null;
    } catch {
        return null;
    }

}

function isValidDate(d, minDate?: Date, maxDate?: Date) {

    if (d.getTime() === d.getTime()) {
        const inputDate = toDateWithoutTime(d);
        //Date is valid but check boundaries
        if (minDate) {
            const minDateAdjusted = toDateWithoutTime(minDate);
            if (inputDate.valueOf() < minDateAdjusted.valueOf())
                return false
        }
        if (maxDate) {
            const maxDateAdjusted = toDateWithoutTime(maxDate);
            if (inputDate.valueOf() > maxDateAdjusted.valueOf())
                return false;
        }
        return true;
    }

    return false;
}

function toDateWithoutTime(inputDate: Date) {
    const date = new Date(inputDate.getFullYear(), inputDate.getMonth(), inputDate.getDate());
    return date;
}

function toFormDisplay(date: Date, format?: DateFormat) {
    if (!date) {
        return "Enter date"
    } else {
        const m = moment(date);
        const pattern = format ? format : "DD/MM/YYYY"
        return m.format(pattern);
    }
}

function parseFormInput(dateString: string, format?: DateFormat, minDate?: Date, maxDate?: Date) {
    try {
        const pattern = format ? format : "DD/MM/YYYY"
        var m = moment(dateString, pattern)
        const result = m.toDate();
        if (isValidDate(result, minDate, maxDate)) {
            return result;
        }

        return null;
    } catch {
        return null;
    }
}

export const dateUtil = {
    toReadableTimeString: toReadableTimeString,
    toMonthYearString: toMonthYearString,
    parseMonthYearString: parseMonthYearString,
    toFormDisplay: toFormDisplay,
    parseFormInput: parseFormInput
}