import { Spacer } from "components/Spacer"
import { colors } from "constants/colors"
import { measurementConstants } from "constants/measurementConstants"
import React, { useState } from "react"
import { StyleSheet, View } from "react-native"
import { SortDirection } from "types/shared"
import { OverviewHeaderText } from "./OverviewHeaderText"



interface Props {

    onToggleSort: (label: string, sortDirection: SortDirection) => void
}
export const OverviewHeader = (props: Props) => {

    const [sortLabel, setSortLabel] = useState(null);
    const [sortDirection, setSortDirection] = useState<SortDirection>(SortDirection.down)

    const onToggleSort = (label: string) => {
        let newSortDirection: SortDirection = sortDirection

        if (label === sortLabel) {
            //Change direction
            if (sortDirection === SortDirection.down) {
                newSortDirection = SortDirection.up
                setSortDirection(newSortDirection)
            } else {
                newSortDirection = SortDirection.down
                setSortDirection(newSortDirection)
            }
        } else {
            setSortLabel(label)
        }

        props.onToggleSort(sortLabel, newSortDirection)
    }


    return (
        <View style={[styles.container, styles.row]}>
            <View style={styles.leftColumn}>
                <View style={styles.innerRow}>
                    <View style={styles.innerColumn}>
                        <OverviewHeaderText onToggleSort={onToggleSort}
                            sortDirection={sortDirection}
                            sortLabel={sortLabel} label="Registration" />
                    </View>
                    <View style={styles.innerColumn}>
                        <OverviewHeaderText label="Status" />
                    </View>
                </View>
                <Spacer small />
                <OverviewHeaderText label="Vehicle Description" />
            </View>
            <View style={styles.leftColumn}>
                <OverviewHeaderText label="Sub-Status" />
                <Spacer small />
                <OverviewHeaderText label="Contact Name" />
            </View>
            <View style={styles.rightColumn}>
                <OverviewHeaderText onToggleSort={onToggleSort}
                    sortDirection={sortDirection}
                    sortLabel={sortLabel} label="In Date" />
                <Spacer small />
                <OverviewHeaderText label={"Contact \nNumber"} />
            </View>
            <View style={styles.rightColumn}>
                <OverviewHeaderText onToggleSort={onToggleSort}
                    sortDirection={sortDirection}
                    sortLabel={sortLabel} label="Out Date" />
                <Spacer small />
                <OverviewHeaderText label="Valet" />
            </View>
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        backgroundColor: colors.default,
        marginHorizontal: -(measurementConstants.cardHorizontalPadding + 2),
        paddingHorizontal: (measurementConstants.cardHorizontalPadding + 2),
        paddingVertical: 10,
        borderRadius: 4
    },
    row: {
        flexDirection: 'row'
    },
    leftColumn: {
        flex: 2
    },
    rightColumn: {
        flex: 1,
    },
    innerColumn: {
        flex: 1,
        flexDirection: 'row'
    },
    innerRow: {
        flexDirection: 'row'
    }
})