import { SignatureBuilder } from "builders/SignatureBuilder"
import { RWeakText } from "components/Typography/RWeakText"
import { colors } from "constants/colors"
import { measurementConstants } from "constants/measurementConstants"
import React, { useEffect, useRef, useState } from "react"
import { Image, Modal, Pressable, StyleSheet, TouchableWithoutFeedback, View } from "react-native"
import { ReactSketchCanvas } from "react-sketch-canvas"
import { SignatureButton } from "./SignatureButton"

interface Props {

}
export const Signature = (props: Props) => {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [image, setImage] = useState(null);
    const sketchRef = useRef(null);
    useEffect(() => {
    }, [])
    const save = () => {

    }

    const getBase64Callback = (err, result) => {
        const base64 = `data:image/jpeg;base64, ${result}`
        SignatureBuilder.getInstance().setSignature(base64)
        setImage(base64)
    }


    const openModal = () => {
        setIsModalVisible(true);
    }

    const closeModal = () => {
        setIsModalVisible(false);

    }

    const saveSignature = async () => {
        closeModal();
        const result = await sketchRef.current.exportImage("png");

        SignatureBuilder.getInstance().setSignature(result);
        setImage(result)
    }

    const onPress = () => {
        openModal();
    }

    const renderImage = () => {
        if (image) {
            return <Image source={{ uri: image }} style={styles.image} />

        } else {
            return (
                <View style={styles.textOverlay}>
                    <RWeakText>Tap to sign</RWeakText>
                </View>
            )

        }
    }



    return (
        <View>
            <Modal visible={isModalVisible}
                animationType="fade"
                transparent={true} presentationStyle="overFullScreen">
                <View style={styles.modalContainer}  >
                    <TouchableWithoutFeedback onPress={saveSignature}>
                        <View style={{ flex: 1 }} />
                    </TouchableWithoutFeedback>
                    <View
                        style={styles.modalBody}>
                        <View style={styles.canvasInModalBorder}>
                            <ReactSketchCanvas
                                ref={sketchRef}
                                style={styles.sketchCanvas}
                                strokeWidth={4}
                                strokeColor="red"
                            />
                        </View>
                    </View>

                    <SignatureButton onPress={saveSignature} label="Confirm" />

                    <TouchableWithoutFeedback onPress={saveSignature}>
                        <View style={{ flex: 1 }} />
                    </TouchableWithoutFeedback>
                </View>

            </Modal>
            <Pressable style={styles.border} onPress={onPress}  >
                <View
                    style={styles.canvas}
                >
                    {renderImage()}
                </View>
            </Pressable>
        </View>

    )
}

const styles = StyleSheet.create({
    canvas: {
        height: measurementConstants.signatureHeight,
        width: '100%',
        backgroundColor: 'white',

    },
    border: {
        borderWidth: 1,
        borderRadius: 4,
        borderColor: colors.themeDarkGrey
    },
    verbalRow: {
        marginRight: 20,
        marginTop: 10
    },
    bottomRow: {
        flexDirection: 'row'
    },
    clearButton: {
        marginRight: 20
    },
    modalBody: {
        width: '100%',
        borderRadius: measurementConstants.modalButtonBorderRadius,
        backgroundColor: 'white',
        padding: 20
    },
    canvasInModalBorder: {
        borderWidth: 1,
        borderRadius: measurementConstants.modalButtonBorderRadius,
        borderColor: colors.themeDarkGrey,
        height: measurementConstants.signatureHeight,
        padding: 4,
        backgroundColor: 'white'
    },
    modalContainer: {
        flex: 1,
        backgroundColor: colors.modalBackground

    },
    image: {
        flex: 1
    },
    textOverlay: {
        justifyContent: 'center',
        alignItems: 'center',
        flex: 1
    },
    sketchCanvas: {
        borderWidth: 1,
        borderRadius: 4,
        borderColor: 'black'
    }
})