import { colors } from 'constants/colors';
import React from 'react';
import { StyleSheet, View } from 'react-native';
import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

interface Props {
    number: number,
    highlight?: boolean
}
export const NumericIcon = (props: Props) => {
    const iconName = `numeric-${props.number}`
    const backgroundColor = props.highlight ? colors.primary : colors.default
    return (
        <View style={styles.outerContainer}>
            <View style={{ ...styles.container, backgroundColor: backgroundColor }}>
                <MaterialCommunityIcons name={iconName} color='white' size={26} />


            </View>
        </View>


    )
}

const styles = StyleSheet.create({
    container: {
        borderRadius: 20,
        width: 30,
        height: 30,
        backgroundColor: colors.themeDarkBlue,
        justifyContent: 'center',
        alignItems: 'center',

    },
    outerContainer: {
        paddingVertical: 8,
        backgroundColor: 'white',
        justifyContent: 'center',
        alignItems: 'center'
    }

})