import { createNativeStackNavigator } from '@react-navigation/native-stack';
import React, { useEffect, useState } from 'react';
import { Platform } from 'react-native';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import { AdditionalJobsScreen } from 'screens/AdditionalJobsScreen';
import { AdditionalTermsScreen } from 'screens/AdditionalTermsScreen';
import { ButterflyScreen } from 'screens/ButterflyScreen';
import { CameraScreen } from 'screens/CameraScreen';
import { CheckInScreen } from 'screens/CheckInScreen';
import { CustomerConcernScreen } from 'screens/CustomerConcernScreen';
import { EditCustomerScreen } from 'screens/EditCustomer';
import { HomeScreen } from 'screens/HomeScreen';
import { JobcardScreen } from 'screens/JobcardScreen';
import { LoanCheckOutScreen } from 'screens/LoanCheckOutScreen';
import { MarkScreen } from 'screens/MarkScreen';
import { NotesScreen } from 'screens/NotesScreen';
import { OverviewScreen } from 'screens/OverviewScreen';
import { PhotosScreen } from 'screens/PhotosScreen';
import { PromotionsScreen } from 'screens/PromotionsScreen';
import { SelectDepartmentScreen } from 'screens/SelectDepartmentScreen';
import { ServiceHistoryScreen } from 'screens/ServiceHistoryScreen';
import { ScreenName } from 'types/navigation';
import { orientationUtil } from 'util/orientationUtil';
import { saveUtil } from 'util/saveUtil';
import { soundUtil } from 'util/soundUtil';
import { splashUtil } from 'util/splashUtil';
import { FontLoader } from './src/FontLoader';

const Stack = createNativeStackNavigator();

/**
 * Remove logging in production
 */
if (!__DEV__) {
  console.log = () => { };
}

const screenOptions = {
  headerShown: false,
  animation: Platform.OS === 'android' ? 'none' : 'default'
}



export default function App() {
  const [isReady, setIsReady] = useState(false);
  const [fontsLoaded, setFontsLoaded] = useState(false);

  useEffect(() => {
    splashUtil.preventAutoHide();
    splashUtil.hideFakeSplash();
  }, [])

  useEffect(() => {
    soundUtil.init();
  }, [])

  useEffect(() => {
    orientationUtil.lockToPortrait();
    setup();
  }, [])


  const setup = async () => {
    await saveUtil.loadSavedObject();
    setIsReady(true);
  }

  if (!isReady || !fontsLoaded) {
    return <FontLoader onLoaded={() => setFontsLoaded(true)} />;
  }




  return (
    <SafeAreaProvider>
      {/*//@ts-ignore*/}

      <Stack.Navigator screenOptions={screenOptions} initialRouteName={ScreenName.Home} >
        <Stack.Screen name={ScreenName.Overview}
          component={OverviewScreen} />
        <Stack.Screen name={ScreenName.AdditionalJobs}
          component={AdditionalJobsScreen} />
        <Stack.Screen name={ScreenName.Jobcard}
          component={JobcardScreen} />
        <Stack.Screen name="Promotions"
          component={PromotionsScreen} />
        <Stack.Screen name={ScreenName.SelectDepartment}
          component={SelectDepartmentScreen}
        />
        <Stack.Screen name={ScreenName.EditCustomer}
          component={EditCustomerScreen}
        />
        <Stack.Screen name={ScreenName.Notes}
          component={NotesScreen} />
        <Stack.Screen name={ScreenName.ServiceHistory}
          component={ServiceHistoryScreen} />
        <Stack.Screen name={ScreenName.Butterfly}
          component={ButterflyScreen} />
        <Stack.Screen name={ScreenName.AdditionalTerms}
          component={AdditionalTermsScreen} />
        <Stack.Screen name={ScreenName.CheckIn}
          component={CheckInScreen} />
        <Stack.Screen name={ScreenName.Photos}
          component={PhotosScreen} />
        <Stack.Screen name={ScreenName.Camera}
          component={CameraScreen} />
        <Stack.Screen name={ScreenName.Mark}
          component={MarkScreen} />
        <Stack.Screen name={ScreenName.Home}
          component={HomeScreen} />
        <Stack.Screen name={ScreenName.CustomerConcern}
          component={CustomerConcernScreen} />
        <Stack.Screen name={ScreenName.LoanCheckOut}
          component={LoanCheckOutScreen} />
      </Stack.Navigator>

    </SafeAreaProvider>

  );
}

