import { RLink } from "components/Buttons/RLink"
import { View } from "react-native"
import { ScreenName } from "types/navigation"
import { navigationUtil } from "util/navigationUtil"

interface Props {
    label: string,
    screen: ScreenName
}

export const FooterLink = (props: Props) => {
    const onPress = () => {
        navigationUtil.navigate(props.screen)
    }
    return (
        <View>
            <RLink onPress={onPress} small label={props.label}></RLink>
        </View>

    )
}
