import { RIcon } from "components/RIcon";
import { colors } from "constants/colors";
import { fontSizes } from "constants/fontSizes";
import React from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
import { Text } from "react-native-paper";
/**
 * This button will fill up space
 */
interface Props {

    label: string,
    onPress?: () => void,
    small?: boolean,
    disabled?: boolean,
    icon: string
}
export const IconLink = (props: Props) => {
    const fontSize = props.small ? fontSizes.small : fontSizes.normal
    const textStyle = [styles.text, { fontSize: fontSize }]
    if (props.disabled) {
        //@ts-ignore
        textStyle.push(styles.disabled)
    }
    if (props.onPress && !props.disabled) {
        return (
            <TouchableOpacity onPress={props.onPress} style={styles.container}>
                <RIcon icon={"pencil"} color={colors.primary} />
                <Text style={textStyle}>{props.label}</Text>
            </TouchableOpacity>
        )
    } else {
        return (
            <View style={styles.container}>
                <RIcon icon={"pencil"} />
                <Text style={textStyle}>{props.label}</Text>
            </View>
        )
    }

}

const styles = StyleSheet.create({
    text: {
        marginLeft: 6,
        color: colors.themeDarkBlue,
    },
    disabled: {
        color: 'black'
    },
    container: {
        flexDirection: 'row',
        alignItems: 'center'
    }
})