import { configureStore } from '@reduxjs/toolkit'
import { selectDepartmentReducer } from 'reducers/selectDepartment'
import { sharedReducer } from 'reducers/shared'
import { splashReducer } from 'reducers/splashReducer'
import { bottomMessageReducer } from './bottomMessage'
import { cameraReducer } from './camera'
import { editCustomerReducer } from './editCustomer'
import { photosReducer } from './photos'

export const store = configureStore({
    reducer: {
        bottomMessage: bottomMessageReducer,
        shared: sharedReducer,
        selectDepartment: selectDepartmentReducer,
        splash: splashReducer,
        camera: cameraReducer,
        editCustomer: editCustomerReducer,
        photos: photosReducer
    },
})

// Infer the `RootState` and `AppDispatch` types from the store itself
export type RootState = ReturnType<typeof store.getState>
// Inferred type: {posts: PostsState, comments: CommentsState, users: UsersState}
export type AppDispatch = typeof store.dispatch
