import { HeaderText } from 'components/Typography/HeaderText';
import { Camera, CameraCapturedPicture, CameraType } from 'expo-camera';
import React, { useEffect, useRef, useState } from 'react';
import { StyleSheet, Text, View } from 'react-native';
import { useDispatch } from 'react-redux';
import { fileUtil } from 'util/fileUtil';

import { orientationUtil } from 'util/orientationUtil';
import { CameraBottomSection } from '../CameraBottomSection';

interface Props {
    previewWidth: number,
    previewHeight: number,
    onSavePhoto: (filePath: string) => void
}

export const CameraModalBodyPhone = (props: Props) => {
    const [hasPermission, setHasPermission] = useState(null);
    const cameraRef = useRef(null);

    const [isProcessing, setIsProcessing] = useState(false);
    const [blackHeight, setBlackHeight] = useState(0);
    const [isBusy, setIsBusy] = useState(false);
    const dispatch = useDispatch();

    useEffect(() => {
        (async () => {
            const { status } = await Camera.requestCameraPermissionsAsync();
            setHasPermission(status === 'granted');
        })();

        orientationUtil.lockToPortrait();

    }, []);



    async function savePicture(filePath: string) {

        props.onSavePhoto(filePath);
    };

    /**
     * Only called by Expo camera
     * @param picture 
     */
    const onPictureSaved = async (picture: CameraCapturedPicture) => {
        const filePath = await fileUtil.createImageWithExpo(picture.base64, true)
        savePicture(filePath);
    }

    /**
     * On IoS and Android the picture will be jpg
     */
    const takePhoto = async () => {
        setIsBusy(true);
        let camera: Camera = cameraRef.current;
        const options = {
            base64: true,
            onPictureSaved: onPictureSaved,
            quality: 1
        }
        camera.takePictureAsync(options);
    }

    const pickPhoto = async () => {
        setIsProcessing(true);


    }

    if (hasPermission === null) {
        return <Text>No access to camera</Text>;
    }
    if (hasPermission === false) {
        return <Text>No access to camera</Text>;
    }

    if (isProcessing) {
        return (
            <View style={styles.waitContainer}>
                <HeaderText>
                    Loading image...
                </HeaderText>

            </View>
        )
    }

    const containerStyle = {
        width: props.previewWidth,
        height: props.previewHeight
    }

    const onLayout = (event) => {
        var { x, y, width, height } = event.nativeEvent.layout;
        console.log("onLayout", height);
        setBlackHeight(height);
    }

    return (
        <View style={styles.container}>
            <View style={styles.black} >
            </View>
            <View style={containerStyle}>
                <Camera style={styles.camera} type={CameraType.back} useCamera2Api ratio="4:3" ref={cameraRef}>

                </Camera>
            </View>
            <View style={styles.black} onLayout={onLayout}>
                <CameraBottomSection label="Take Photo"
                    onPressCamera={takePhoto} onPressPickPhoto={pickPhoto}
                    blackHeight={blackHeight}
                    isBusy={isBusy} />

            </View>



        </View>

    );
}



const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: 'black'
    },
    waitContainer: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: 'white'
    },
    black: {
        flex: 1,
        backgroundColor: 'black',
        justifyContent: 'space-evenly',
        alignItems: 'center'
    },
    camera: {
        flex: 1,
    },
    takePhotoButtonContainer: {
        position: 'absolute',
        bottom: 50,
        right: 0,
        left: 0,
        height: 200,
        alignItems: 'center',
        justifyContent: 'flex-end'
    },
    checkboxContainer: {
        position: 'absolute',
        top: 50,
        left: 0,
        right: 0,
        height: 60,
        justifyContent: 'center',
        alignItems: 'center',
    },
    buttonBorder: {
        borderWidth: 3,
        borderColor: 'white',
        borderRadius: 50,
        width: 60,
        height: 60,
        justifyContent: 'center',
        alignItems: 'center'
    },
    pickContainer: {
        position: 'absolute',
        bottom: 0,
        left: 10,
        height: 60,
        justifyContent: 'center',
    },
});

