import { ImageRequestBuilder } from 'builders/ImageRequestBuilder';
import { measurementConstants } from 'constants/measurementConstants';
import { Camera } from 'expo-camera';
import React, { useEffect, useState } from 'react';
import { Text, useWindowDimensions } from 'react-native';
import { useDispatch } from 'react-redux';
import { photosActions } from 'reducers/photos';
import { navigationUtil } from 'util/navigationUtil';

import { orientationUtil } from 'util/orientationUtil';
import { CameraModalBodyPhone } from './CameraModalBodyPhone';
import { CameraModalBodyTablet } from './CameraModalBodyTablet';

enum CameraLayout {
    Unknown,
    Phone,
    Tablet
}

export const CameraModalBody = () => {
    const [hasPermission, setHasPermission] = useState(null);
    const windowDimensions = useWindowDimensions();
    const [previewWidth, setPreviewWidth] = useState(null);
    const [previewHeight, setPreviewHeight] = useState(null);
    const [cameraLayout, setCameraLayout] = useState<CameraLayout>(CameraLayout.Unknown);
    const dispatch = useDispatch();

    useEffect(() => {
        (async () => {
            const { status } = await Camera.requestCameraPermissionsAsync();
            setHasPermission(status === 'granted');
        })();

        orientationUtil.lockToPortrait();

    }, []);

    useEffect(() => {
        const previewHeight = windowDimensions.width * 4 / 3;
        const previewWidth = windowDimensions.width
        setPreviewWidth(previewWidth)
        setPreviewHeight(previewHeight)
        if (previewHeight > windowDimensions.height - measurementConstants.cameraBlackBottom * 2) {
            setCameraLayout(CameraLayout.Tablet)
        } else {
            setCameraLayout(CameraLayout.Phone)

        }
    }, [windowDimensions])

    const onSavePhoto = (filePath: string) => {
        ImageRequestBuilder.getInstance().addLocalPhoto(filePath);
        dispatch(photosActions.registerChange());
        navigationUtil.back();
    }

    if (hasPermission === null) {
        return <Text>No access to camera</Text>;
    }
    if (hasPermission === false) {
        return <Text>No access to camera</Text>;
    }
    if (!previewHeight || !previewWidth) {
        return null;
    }
    if (cameraLayout === CameraLayout.Unknown) {
        return (
            null
        )
    }

    if (cameraLayout === CameraLayout.Phone) {
        return <CameraModalBodyPhone previewHeight={previewHeight} previewWidth={previewWidth} onSavePhoto={onSavePhoto} />
    }

    return <CameraModalBodyTablet previewHeight={previewHeight} previewWidth={previewWidth} onSavePhoto={onSavePhoto} />


}
