import { RCard } from "components/RCard";
import { RCheckBox } from "components/RCheckBox";
import React, { useState } from "react";
import { StyleSheet, View } from "react-native";

export const WaitJobSection = () => {

    const [waitJob, setWaitJob] = useState(false);
    const [loanCar, setLoanCar] = useState(false);
    const [shuttleBus, setShuttleBus] = useState(false);
    const [valet, setValet] = useState(false);

    return (
        <RCard >

            <View style={styles.row}>
                <View style={styles.column}>
                    <RCheckBox onValueChanged={setWaitJob} label={"Wait Job"} isChecked={waitJob} />
                </View>

                <View style={styles.column}>
                    <RCheckBox onValueChanged={setLoanCar} label={"Loan Car"} isChecked={loanCar} />
                </View>

                <View style={styles.column}>
                    <RCheckBox onValueChanged={setShuttleBus} label={"Shuttle Bus"} isChecked={shuttleBus} />
                </View>

                <View style={styles.column}>
                    <RCheckBox onValueChanged={setValet} label={"Valet"} isChecked={valet} />
                </View>

            </View>


        </RCard>

    );
}

export const styles = StyleSheet.create({
    column: {
        flex: 1,
        paddingRight: 40,
    },
    rightColumn: {
        flex: 1,
        paddingRight: 40,
        alignItems: 'flex-end'
    },
    row: {
        flexDirection: 'row'
    }

})
