import { AndroidNativeProps, DateTimePickerAndroid, DateTimePickerEvent } from "@react-native-community/datetimepicker";
import { DateInputFormControl } from "components/DateInput/DateInputFormControl";
import { DateInputManualEntry } from "components/DateInput/DateInputManualEntry";
import { DateInputSmallButton } from "components/DateInput/DateInputSmallButton";
import { DatePickerIos } from "components/DateInput/DatePickerIos";
import { colors } from "constants/colors";
import { measurementConstants } from "constants/measurementConstants";
import React, { useState } from "react";
import { Platform, StyleSheet, View } from "react-native";
import { DateFormat } from "types/date";
import { DateType } from "types/shared";
import { dateUtil } from "util/dateUtil";

interface Props {
    label: string,
    value: Date,
    onChangeDate: (text: Date) => void,
    minimumDate?: Date,
    maximumDate?: Date,
    disabled?: boolean,
    format?: DateFormat
    display?: 'FormControl' | 'SmallButton',
    type?: DateType
}



export const DateInput = (props: Props) => {
    const [iosPickerVisible, setIosPickerVisible] = useState(false);
    const [showManual, setShowManual] = useState(false);

    const onPress = () => {
        setShowManual(true);
    }

    /**
     * Open the picker
     */
    const onPressIcon = () => {
        if (props.disabled) {
            return;
        }

        if (Platform.OS === 'ios') {
            setIosPickerVisible(true);

        } else {
            const params: AndroidNativeProps = {
                value: props.value ? props.value : new Date(),
                onChange: onChangeAndroidDate
            }
            DateTimePickerAndroid.open(params)

        }
    }

    const onDismissIos = React.useCallback(() => {
        setIosPickerVisible(false);
    }, [setIosPickerVisible]);


    const onConfirmIosPickerDate = (date: Date) => {
        props.onChangeDate(date);
        setIosPickerVisible(false);
    };

    const onChangeAndroidDate = (event: DateTimePickerEvent, date: Date) => {
        if (event.type === 'set')
            props.onChangeDate(date);
    }


    const onCancelManual = () => {
        setShowManual(false);
    }

    const onConfirmManual = (date: Date) => {
        setShowManual(false);
        props.onChangeDate(date)
    }

    let label = props.disabled ? "" : "Enter date";

    if (props.value) {
        label = dateUtil.toFormDisplay(props.value, props.format)
    }
    return (
        <View style={styles.container} >
            {props.display === 'SmallButton'  &&
                <DateInputSmallButton label={label} onPress={onPressIcon} />
            }
            {(props.display === 'FormControl' || props.display === undefined) &&
                <DateInputFormControl heading={props.label} label={label} onPress={onPress}
                    disabled={props.disabled} onPressIcon={onPressIcon} />
            }


            <DatePickerIos visible={iosPickerVisible} onDismiss={onDismissIos}
                date={props.value} onConfirm={onConfirmIosPickerDate} minimumDate={props.minimumDate}
                maximumDate={props.maximumDate} />

            {showManual &&
                <DateInputManualEntry
                    format={props.format}
                    heading={props.label}
                    onCancel={onCancelManual}
                    onChangeDate={onConfirmManual}
                    value={props.value ? props.value : new Date()}
                    minimumDate={props.minimumDate}
                    maximumDate={props.maximumDate}
                />}

        </View>
    )
}


const styles = StyleSheet.create({
    textInput: {
        fontSize: 20,
        fontFamily: 'RobotoCondensed-Bold',
        color: colors.black,
        paddingTop: 0,
        flex: 1,
        borderBottomWidth: 1,
        borderBottomColor: colors.themeDarkGrey,
    },
    container: {


    },
    row: {
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: measurementConstants.formComponentMarginBottom,
    },
    iconContainer: {
        justifyContent: 'center',
        alignItems: 'center',
        paddingLeft: 30,
    }
})