import { colors } from "constants/colors";
import { textInputStyle } from "constants/textInputConfig";
import React from "react";
import { ReturnKeyTypeOptions, StyleSheet, Text, TextInput, View } from "react-native";
import { IconButton } from "react-native-paper";

interface Props {
    label?: string,
    onChangeText: (text) => void,
    isIconVisible: boolean,
    icon: string,
    /**
     * This is pressing the cancel field icon
     */
    onPressIcon?: () => void
    value: string,
    placeholder?: string
    autoCapitalize?: boolean,
    onPressKeyboardReturn?: () => void,
    /**
     * Set to 'search' for search inputs
     */
    returnKeyType?: ReturnKeyTypeOptions,
    multiline?: boolean
}
export const RTextInputOutlined = (props: Props) => {

    const autoCapitalize = props.autoCapitalize ? 'characters' : undefined


    return (
        <View style={styles.container} >
            {props.label && <Text style={styles.label}>{props.label}</Text>}
            <View>
                <TextInput blurOnSubmit={true}
                    value={props.value}
                    onChangeText={props.onChangeText}
                    style={styles.textInput}
                    placeholder={props.placeholder}
                    placeholderTextColor={colors.placeholder}
                    autoCapitalize={autoCapitalize}
                    onSubmitEditing={props.onPressKeyboardReturn}
                    returnKeyType={props.returnKeyType}
                    multiline={props.multiline}
                />
                {props.isIconVisible &&
                    <View style={styles.textInputOverlay}>
                        <IconButton icon={props.icon} onPress={props.onPressIcon} color={colors.themeDarkGrey} />
                    </View>
                }
            </View>

        </View>)
}

const styles = StyleSheet.create({
    textInput: {
        ...textInputStyle,
        paddingLeft: 6,
        paddingVertical: 12,
        borderWidth: 1,
        borderColor: colors.themeLightGrey,
        borderRadius: 5,
        backgroundColor: 'white',
    },
    container: {
        flex: 1


    },
    label: {
        fontFamily: 'RobotoCondensed-Light',
        paddingBottom: 10
    },
    textInputOverlay: {
        position: 'absolute',
        right: 0,
        top: 0,
        bottom: 0,
        justifyContent: 'center'
    }
})