import { RButton } from "components/Buttons/RButton"
import { CompanyDataView } from "components/SelectDepartment/CompanyDataView"
import { SelectDepartmentInput } from "components/SelectDepartment/SelectDepartmentInput"
import { colors } from "constants/colors"
import React from "react"
import { ScrollView, StyleSheet, View } from "react-native"
import { useSelector } from "react-redux"
import { RootState } from "reducers/store"
import { LoginResponseData } from "types/login"

interface Props {
    data: LoginResponseData
}

export const LoginResponseDataView = (props: Props) => {
    const isNextButtonVisible = useSelector((state: RootState) => state.selectDepartment.isNextButtonVisible);

    const onPressNext = () => {
        //navigationUtil.navigate( )
    }


    return (

        <View style={styles.container}>
            <SelectDepartmentInput />
            {!isNextButtonVisible &&
                <View style={styles.bottomContainer}>
                    <View style={styles.selectionContainerBorder}>
                        <ScrollView>
                            <View style={styles.selectionContent}>
                                {props.data.companyData.map((item, index) => {
                                    return <CompanyDataView data={item} key={item.id} />
                                })}
                            </View>

                        </ScrollView>
                    </View>
                </View>
            }


            {isNextButtonVisible &&
                <RButton icon="chevron-right" label="Next" onPress={onPressNext} />
            }

        </View>



    )
}
const styles = StyleSheet.create({
    container: {
        flex: 1,
        width: '100%',
    },
    selectionContainerBorder: {
        borderColor: colors.themeLightGrey,
        borderWidth: 1,
        borderTopWidth: 0,
        borderRadius: 5
    },
    bottomContainer: {
        flex: 1,
        paddingBottom: 100
    },
    selectionContent: {
        paddingHorizontal: 10,
    }
})