import React from "react";
import { TouchableOpacity, View } from "react-native";
import { cardStyles } from "./cardStyles";

interface Props {
    children: any,
    flex?: boolean,
    onPress?: () => void,
    noMargin?: boolean
}

export const RCard = (props: Props) => {
    const innerStyle = [styles.shadow];
    const outerStyle = [styles.wrapper]
    const touchableStyle = [styles.touchable]
    if (props.flex) {
        //@ts-ignore
        innerStyle.push({ flex: 1 })
        //@ts-ignore
        outerStyle.push({ flex: 1 })
        //@ts-ignore
        touchableStyle.push({ flex: 1 })
    }

    if (props.noMargin) {
        //@ts-ignore
        outerStyle.push(styles.noMargin)
    }

    const onPress = () => {
        if (props.onPress) {
            props.onPress()
        }
    }

    const renderBody = () => {
        if (props.onPress) {
            return (
                <TouchableOpacity onPress={onPress} style={touchableStyle}  >
                    {props.children}
                </TouchableOpacity>
            )
        }

        return (
            <View style={touchableStyle}  >
                {props.children}
            </View>
        )

    }

    return (
        //onStartShouldSetResponder is used to enable scroll when FlatList is inside RScreenContainer
        <View style={outerStyle} onStartShouldSetResponder={() => true}>
            <View style={innerStyle}  >
                {renderBody()}
            </View>

        </View>



    )


}


const styles = cardStyles