import { RText } from "components/Typography/RText"
import { RWeakText } from "components/Typography/RWeakText"
import React from "react"
import { StyleSheet, TouchableOpacity, View } from "react-native"


interface Props {
    children: any,
    level: 1 | 2 | 3,
    onPress?: () => void
}

export const SelectDepartmentText = (props: Props) => {
    const marginLeft = (props.level - 1) * 6

    const onPress = () => {
        if (props.onPress) {
            props.onPress();
        }

    }
    if (props.level === 3) {
        return (
            <TouchableOpacity onPress={onPress}>
                <View style={{ ...styles.container, marginLeft: marginLeft }}>
                    <RWeakText>{props.children}</RWeakText>
                </View>
            </TouchableOpacity>

        )
    }
    else {
        return (
            <View style={{ ...styles.container, marginLeft: marginLeft }}>
                <RText>{props.children}</RText>
            </View>
        )
    }

}

const styles = StyleSheet.create({
    container: {
        marginVertical: 6
    }
})

