import { RCheckBox } from "components/RCheckBox";
import { RText } from "components/Typography/RText";
import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, View } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { editCustomerActions } from "reducers/editCustomer";
import { RootState } from "reducers/store";
import { ContactMethod } from "types/editCustomer";

interface Props {
    type: ContactMethod
}

export const PhoneChildComponent = (props: Props) => {

    const label = `- ${props.type}`
    const declinedMethods = useSelector((state: RootState) => state.editCustomer.declinedMethods);
    const isDisabled = declinedMethods.includes(props.type)
    const preferredContact = useSelector((state: RootState) => state.editCustomer.preferredContact)
    const dispatch = useDispatch();

    const onCheckChanged = (value: boolean) => {
        let payload = null
        if (value) {
            payload = props.type
        }

        dispatch(editCustomerActions.updatePreferredContact(payload))
    }

    const isChecked = preferredContact === props.type

    if (isDisabled) {
        return null;
    }
    return (

        <View style={styles.row} >
            <View style={styles.column}>
                <RText containerStyle={styles.indent}>{label}</RText>
            </View>
            <View style={styles.column}>

            </View>
            <View style={styles.column}>
                <RCheckBox onValueChanged={onCheckChanged} label={""} isChecked={isChecked} />
            </View>
            <View style={{ flex: 1 }} />
        </View>

    )
}


const styles = StyleSheet.create({
    row: {
        flexDirection: 'row',
        paddingBottom: measurementConstants.formComponentMarginBottomSmall
    },
    column: {
        flex: 1
    },
    indent: {
        paddingLeft: 30,
        flex: 1
    },

})
