import { RCheckBox } from "components/RCheckBox";
import { Spacer } from "components/Spacer";
import { RText } from "components/Typography/RText";
import React from "react";
import { View } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { editCustomerActions } from "reducers/editCustomer";
import { RootState } from "reducers/store";
import { ContactMethod } from "types/editCustomer";

interface Props {
    contactMethod: ContactMethod
}


export const PreferenceComponent = (props: Props) => {

    const dispatch = useDispatch();
    const preferredContact = useSelector((state: RootState) => state.editCustomer.preferredContact);
    const allowedContacts = useSelector((state: RootState) => state.editCustomer.allowedContactMethods);
    const declinedMethods = useSelector((state: RootState) => state.editCustomer.declinedMethods);
    const isAllowedContact = allowedContacts.indexOf(props.contactMethod) > -1;
    const isDisabled = declinedMethods.indexOf(props.contactMethod) > -1;

    const setIsPreferred = (value: boolean) => {
        if (value) {
            dispatch(editCustomerActions.updatePreferredContact(props.contactMethod))

        } else {
            dispatch(editCustomerActions.updatePreferredContact(null))

        }
    }

    const setIsContactable = (value: boolean) => {
        if (value)
            dispatch(editCustomerActions.addAllowedContactMethod(props.contactMethod));
        else
            dispatch(editCustomerActions.removeAllowedContactMethod(props.contactMethod))
    }

    const label = props.contactMethod?.toString();
    const isPreferredChecked = preferredContact === props.contactMethod;

    if (isDisabled) {
        return null;
    }

    return (
        <View>
            <View style={{ flexDirection: 'row' }} >
                <View style={{ flex: 1 }}>
                    <RText >{label}</RText>
                </View>
                <View style={{ flex: 1 }}>
                    <RCheckBox onValueChanged={setIsContactable} label={""}
                        isChecked={isAllowedContact} />
                </View>
                <View style={{ flex: 1 }}>
                    <RCheckBox onValueChanged={setIsPreferred} label={""}
                        isChecked={isPreferredChecked} />
                </View>
                <View style={{ flex: 1 }} />
            </View>
            <Spacer small />
        </View>
    );
}
