import { ShadedLine } from "components/ShadedLine"
import { RText } from "components/Typography/RText"
import { colors } from "constants/colors"
import React, { useState } from "react"
import { StyleSheet, View } from "react-native"
import { RecallData } from "types/jobcard"
import { RightColumn } from "../RightColumn"


interface Props {
    data: RecallData,
    index: number
}
const _ExcludeLine = (props: Props) => {
    const [renderTime, setRenderTime] = useState(null);

    const onPress = () => {
        props.data.isTicked = !props.data.isTicked
        setRenderTime(new Date());
    }

    const shaded = props.index % 2 === 0;

    return (
        <ShadedLine shaded={shaded} onPress={onPress}>
            <View style={{ flex: 1, justifyContent: 'center' }}>
                <RText>{props.data.text}</RText>

            </View>
            <RightColumn isTicked={props.data.isTicked} />
        </ShadedLine>
    )
}

const propsAreEqual = (prevProps: Props, nextProps: Props) => {
    return true
}

export const ExcludeLine = React.memo(_ExcludeLine, propsAreEqual)

const styles = StyleSheet.create({
    row: {
        borderBottomWidth: 1,
        borderColor: colors.lineSeperator,
        borderRadius: 4,
    },
    bottomRow: {
        flexDirection: 'row',

    },
    icon: {
        marginRight: 10,
        paddingTop: 1
    },
    touchable: {
        width: '100%',
        paddingBottom: 6,
        paddingTop: 6,
        paddingLeft: 14,
        flexDirection: 'row',
        alignItems: 'center'
    },
    rightColumn: {
        justifyContent: 'space-evenly',
        width: 120,
        flexDirection: 'row',
        alignItems: 'center'
    }

})