import { ImageRequestBuilder } from "builders/ImageRequestBuilder";
import { IconLink } from "components/Buttons/IconLlink";
import { RIconButton } from "components/Buttons/RIconButton";
import React from "react";
import { Image, Platform, StyleSheet, View } from "react-native";
import { useDispatch } from "react-redux";
import { photosActions } from "reducers/photos";
import { PhotoCardContainer } from "screens/PhotosScreen/PhotoList/PhotoCardContainer";
import { SimpleImageData } from "types/images";
import { ScreenName } from "types/navigation";
import { navigationUtil } from "util/navigationUtil";

const BORDER_RADIUS = 6;

interface Props {
    data: SimpleImageData,

}
const _PhotoCard = (props: Props) => {
    const dispatch = useDispatch();
    const filePath = props.data.markedFilePath ? props.data.markedFilePath : props.data.filePath

    const deletePhoto = () => {
        ImageRequestBuilder.getInstance().deletePhoto(props.data.id);
        dispatch(photosActions.registerChange())
    }

    /**
     * When press the picture be able to review photo
     */
    const onPress = () => {
        //navigationUtil.navigateToReviewPhoto(filePath)

    }

    const onPressMark = () => {
        navigationUtil.navigate(ScreenName.Mark, props.data);
    }
    const fixedFilePath = Platform.OS === 'android' ? 'file://' + filePath : filePath
    return (
        <PhotoCardContainer onPress={onPress}>
            <View style={styles.imageContainer}>
                <Image source={{ uri: fixedFilePath }} style={styles.image} />

            </View>
            <View style={styles.bottomRow}>
                <IconLink icon="pencil" label="Mark" onPress={onPressMark} />
                <View style={{ flex: 1 }} />
                <RIconButton onPress={deletePhoto} icon={"delete"} />
            </View>


        </PhotoCardContainer>


    )


}

function arePropsEqual(prevProps: Props, nextProps: Props) {

    return true;
}

export const PhotoCard = React.memo(_PhotoCard, arePropsEqual)

const styles = StyleSheet.create({

    image: {
        flex: 1,
        borderTopLeftRadius: BORDER_RADIUS,
        borderTopRightRadius: BORDER_RADIUS
    },
    imageContainer: {
        width: '100%',
        height: 250,
    },
    bottomRow: {
        flexDirection: 'row',
        alignItems: 'center',
        flex: 1,
        paddingHorizontal: 16
    }
})