import { StackActions } from "@react-navigation/native";
import { navigationRef } from "src/RootNavigation";
import { ScreenName } from "types/navigation";

import { hideBottomMessage } from "util/bottomMessageUtil";

function navigateToPreviousScreen(screenName: ScreenName) {
    const routeHistory = navigationRef.getState().routes;
    let foundIndex = -1;
    for (let i = 0; i < routeHistory.length; i++) {
        if (routeHistory[i].name === screenName) {
            foundIndex = i;
            break;
        }
    }

    if (foundIndex >= 0) {
        //If found index is 0 and routeHistory length is 2 then we want to pop 1
        const numPops = routeHistory.length - foundIndex - 1
        const action = StackActions.pop(numPops)

        navigationRef.dispatch(action)
    }
    else {
        navigationUtil.navigate(screenName)
    }
}

export const navigationUtil = {
    back: () => {
        navigationRef.goBack();
    },
    pop: () => {
        const action = StackActions.pop(1)

        navigationRef.dispatch(action)
    },
    popTwice: () => {
        const action = StackActions.pop(2)

        navigationRef.dispatch(action)
    },
    navigate: (screenName: ScreenName, params?: any) => {
        if (navigationRef.isReady()) {
            hideBottomMessage();

            //@ts-ignore
            navigationRef.navigate(screenName, params);
        }
    },
    resetTo: (screenName: ScreenName) => {
        navigateToPreviousScreen(screenName);
    },
}