import { fontSizes } from "constants/fontSizes";
import { textInputStyle } from "constants/textInputConfig";
import React from "react";
import { StyleSheet, Text, TextInput, View } from "react-native";
import { colors } from "../constants/colors";

interface Props {
    label: string,
    value: string,
    multiline?: boolean,
    onChangeText: (text: string) => void,
    small?: boolean,
    masked?: boolean,
    /**
     * Number-pad for non decimals; otherwise use decimal-pad; leave blank for default
     */
    keyboardType?: 'number-pad' | 'decimal-pad' | 'phone-pad' | 'email-address',
}
export const RTextInput = (props: Props) => {
    const fontSize = props.small ? fontSizes.small : fontSizes.normal
    const textStyle = { ...styles.textInput, fontSize: fontSize };
    const autoCapitalize = props.masked ? 'none' : undefined
    const keyboardType = props.keyboardType ? props.keyboardType : 'ascii-capable' //Fix to allow apostrophe on IOS


    return (
        <React.Fragment>
            <View style={[styles.container]} >
                <Text style={styles.label}>{props.label}</Text>
                <TextInput blurOnSubmit={!props.multiline}
                    autoCapitalize={autoCapitalize}
                    secureTextEntry={props.masked}
                    multiline={props.multiline}
                    style={textStyle}
                    value={props.value}
                    onChangeText={props.onChangeText}
                    keyboardType={keyboardType}
                    placeholderTextColor={colors.placeholder} />

            </View>

        </React.Fragment>)
}

const styles = StyleSheet.create({
    textInput: {
        ...textInputStyle
    },
    container: {

        borderBottomWidth: 1,
        borderBottomColor: colors.themeDarkGrey,
        paddingBottom: 5

    },
    label: {
        fontFamily: 'RobotoCondensed-Light',
        paddingBottom: 10,
        fontSize: fontSizes.small,

    }
})