import { colors } from "constants/colors";
import React from "react";
import { StyleSheet } from "react-native";
import { FAB } from 'react-native-paper';
import { ButtonType } from "types/shared";
interface Props {
    icon: string,
    onPress: () => void,
    type: ButtonType
}
/**
 * This component has to be wrapped in an absolute positioned view e.g.
 *  <View style={{ position: 'absolute', bottom: 0, right: 0 }}>
         <CardButton icon="camera" onPress={onPress} type='success' />
    </View>
 */
export const CardButton = (props: Props) => {

    if (props.type === 'success') {
        return (<FAB color='white' style={styles.success}
            icon={props.icon}
            onPress={props.onPress}
        />)
    }

    return <FAB color={colors.success} style={styles.default}
        icon={props.icon}
        onPress={props.onPress}
    />


}

const styles = StyleSheet.create({
    success: {
        backgroundColor: colors.success
    },
    default: {
        backgroundColor: 'white'
    }
})
