import { NumericIcon } from "components/NumberedSection/NumericIcon"
import { Spacer } from "components/Spacer"
import { RText } from "components/Typography/RText"
import { RTextBold } from "components/Typography/RTextBold"
import { colors } from "constants/colors"
import React from "react"
import { StyleSheet, TouchableOpacity, View } from "react-native"

interface Props {
    title: string,
    number: number,
    children: any,
    selectedSection: number
    onPress: () => void,
}
export const NumberedSectionBase = (props: Props) => {
    const selectedSection = props.selectedSection
    const isExpanded = selectedSection === props.number
    const onPress = () => {
        props.onPress()
    }
    if (!isExpanded) {
        return (
            <TouchableOpacity onPress={onPress}>
                <View style={styles.container}>
                    <View style={styles.headerRow}>
                        <View style={styles.numericIconContainer}>
                            <NumericIcon number={props.number} highlight={isExpanded} />

                        </View>
                        <RText small>{props.title.toUpperCase()}</RText>
                    </View>

                </View>
            </TouchableOpacity>

        )
    }
    return (
        <View style={{ ...styles.container, minHeight: 100 }}>
            <View style={styles.headerRow}>
                <View style={styles.numericIconContainer}>
                    <NumericIcon number={props.number} highlight={isExpanded} />

                </View>
                <RTextBold small>{props.title.toUpperCase()}</RTextBold>
            </View>
            <View style={styles.body}>
                {props.children}
            </View>
            <Spacer />

        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        width: '100%',
        borderLeftWidth: 2,
        borderLeftColor: colors.leftBorderedCardBorder,
        marginLeft: 16,
        backgroundColor: 'white'
    },
    headerRow: {
        flexDirection: 'row',
        alignItems: 'center',
        paddingRight: 10
    },
    numericIconContainer: {
        transform: [{ translateX: -16 }],
    },
    body: {
        paddingLeft: 30,
        paddingRight: 20
    }
})