import React from "react";
import { KeyboardAvoidingView, Platform } from "react-native";

interface Props {
    children: any
}
export const RKeyboardAvoidingView = (props: Props) => {
    const keyboardAvoidingEnabled = Platform.OS === 'ios' ? true : false

    return (


        <KeyboardAvoidingView behavior="position" enabled={keyboardAvoidingEnabled}
        >
            {props.children}

        </KeyboardAvoidingView>






    )
}
