import { createSlice } from '@reduxjs/toolkit';

const initialState: State = {
    isCameraModalVisible: false,
    bottomLabel: "Front of Licence"

}

interface State {
    isCameraModalVisible: boolean,
    bottomLabel: string
}


export const slice = createSlice({
    name: 'camera',
    initialState,
    reducers: {
        hideCameralModal: (state) => {
            state.isCameraModalVisible = false
        },
        showCameraModal: (state) => {
            state.isCameraModalVisible = true;
        },

    }
})

export const cameraActions = slice.actions;
export const cameraReducer = slice.reducer
