import { RCheckBox } from "components/RCheckBox";
import { RText } from "components/Typography/RText";
import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, View } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { editCustomerActions } from "reducers/editCustomer";
import { RootState } from "reducers/store";
import { ContactMethod } from "types/editCustomer";
import { PhoneChildComponent } from "./PhoneChildComponent";

interface Props {
}

export const PhonePreferenceComponent = (props: Props) => {
    const allowedContactMethods = useSelector((state: RootState) => state.editCustomer.allowedContactMethods);
    const phoneMethods = [ContactMethod.Mobile, ContactMethod.Home, ContactMethod.Business]
    const isChecked = phoneMethods.some(r => allowedContactMethods.indexOf(r) > -1);
    const dispatch = useDispatch();

    const setIsChecked = (value: boolean) => {
        if (value) {
            phoneMethods.forEach((x) => {
                dispatch(editCustomerActions.addAllowedContactMethod(x))

            })

        } else {
            phoneMethods.forEach((x) => {
                dispatch(editCustomerActions.removeAllowedContactMethod(x))
            })
        }


    }

    return (

        <View>
            <View style={styles.row} >
                <View style={{ flex: 1 }}>
                    <RText>Phone</RText>
                </View>
                <View style={{ flex: 1 }}>
                    <RCheckBox onValueChanged={setIsChecked} label={""} isChecked={isChecked} />
                </View>
                <View style={{ flex: 1 }}>

                </View>
                <View style={{ flex: 1 }} />
            </View>

            <PhoneChildComponent type={ContactMethod.Business} />
            <PhoneChildComponent type={ContactMethod.Home} />
            <PhoneChildComponent type={ContactMethod.Mobile} />
        </View>
    )
}


const styles = StyleSheet.create({
    row: {
        flexDirection: 'row',
        paddingBottom: measurementConstants.formComponentMarginBottomSmall
    },
    indent: {
        paddingLeft: 50,
        flex: 1
    }
})
