import { ImageRequestBuilder } from "builders/ImageRequestBuilder";
import { FloatingButton } from "components/Buttons/FloatingButton";
import { ModalPicker } from "components/ModalPicker";
import React, { useEffect, useState } from "react";
import { StyleSheet, View } from "react-native";
import { useDispatch } from "react-redux";
import { photosActions } from "reducers/photos";
import { PhotoList } from "screens/PhotosScreen/PhotoList";
import { ScreenName } from "types/navigation";
import { imageUtil } from "util/imageUtil";
import { navigationUtil } from "util/navigationUtil";
import { RScreenContainer } from "../../components/RScreenContainer";

enum ModalOption {
    TakePhoto = "Take Photo",
    AddFromGallery = "Add Photo From Gallery"
}

const MODAL_OPTIONS = [ModalOption.TakePhoto, ModalOption.AddFromGallery]

export const PhotosScreen = ({ route }) => {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const dispatch = useDispatch();
    const closeModal = () => { setIsModalVisible(false) }
    const openModal = () => { setIsModalVisible(true) }

    useEffect(() => {
        setup();
    }, [])

    const setup = async () => {

    }

    const onPressFab = () => {
        openModal();
    }

    const optionSelected = async (option: string, index: number) => {
        closeModal();

        if (option === ModalOption.TakePhoto) {
            navigationUtil.navigate(ScreenName.Camera)
        } else if (option === ModalOption.AddFromGallery) {
            const assets = await imageUtil.pickPhoto();
            assets.forEach(x => {
                ImageRequestBuilder.getInstance().addLocalPhoto(x.uri);
                dispatch(photosActions.registerChange())
            })




        }
    }

    return (
        <RScreenContainer title="Photos" noPadding   >
            <View style={styles.photoListContainer}>
                <PhotoList onPressAddPhoto={onPressFab} />
            </View>
            <FloatingButton icon="plus" onPress={onPressFab} />
            <ModalPicker onOptionSelected={optionSelected} options={MODAL_OPTIONS} isVisible={isModalVisible} onCancel={closeModal} />

        </RScreenContainer>


    );
}

const styles = StyleSheet.create({
    photoListContainer: {
        width: '100%',
        marginTop: 10,
        flex: 1
    }
})
