import { RIconButton } from "components/Buttons/RIconButton";
import { RText } from "components/Typography/RText";
import { colors } from "constants/colors";
import React from "react";
import { Keyboard, StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { IconButton } from "react-native-paper";
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { navigateBack } from "src/RootNavigation";
import { JobcardInput } from "./JobcardInput";

interface Props {
    children: any,
    title?: string,
    noPadding?: boolean,
    renderBelowTitle?: () => any
    onPressBack?: () => void,
    hideBack?: boolean,
    onPressMenu?: () => void,
    jobcardId?: string
}
const TOP_CONTAINER_HEIGHT = 40
const PADDING_HORIZONTAL = 44
const PADDING_TOP = 30
export const RScreenContainer = (props: Props) => {
    const insets = useSafeAreaInsets();
    const padding = props.noPadding ? 0 : PADDING_HORIZONTAL
    const paddingTop = props.noPadding ? 0 : PADDING_TOP
    const bodyContainerStyle = [styles.bodyContainer, { paddingHorizontal: padding, paddingTop: paddingTop }]

    const onPressBack = () => {
        if (props.onPressBack) {
            props.onPressBack();
        } else {
            navigateBack();

        }
    }

    const renderBelowTitle = () => {
        if (props.renderBelowTitle) {
            return props.renderBelowTitle()
        } else {
            return null;

        }

    }

    const renderLeftIcon = () => {
        if (props.hideBack) {
            return <View style={{ width: 24 }} />
        } else {
            return <IconButton icon="chevron-left" onPress={onPressBack} />
        }
    }

    const onPressMenu = () => {
        if (props.onPressMenu) {
            props.onPressMenu();
        }
    }


    if (props.title) {
        return (
            <View style={{ flex: 1 }}>
                <View style={{ ...styles.topContainer }} >
                    <View style={styles.shadow}>
                        <View style={{ height: insets.top }} />
                        <View style={styles.headerContainer}>
                            {renderLeftIcon()}
                            <RText>{props.title}</RText>
                            <JobcardInput jobcardId={props.jobcardId} />
                            <View style={{ flex: 1 }} />
                            {props.onPressMenu && <RIconButton icon="menu" onPress={onPressMenu} />}
                        </View>
                        {renderBelowTitle()}

                    </View>
                </View>
                <View style={bodyContainerStyle}>
                    {props.children}
                </View>

            </View>
        )
    } else {
        return (
            <TouchableWithoutFeedback onPress={Keyboard.dismiss} accessible={false}>

                <View style={bodyContainerStyle}>
                    {props.children}
                </View>
            </TouchableWithoutFeedback>
        )
    }



}

const styles = StyleSheet.create({
    bodyContainer: {
        width: '100%',
        flex: 1,
        backgroundColor: 'white',

    },
    topContainer: {
        paddingBottom: 4,
        backgroundColor: 'white'
    },
    topInset: {
        backgroundColor: colors.themeVeryLightGrey,
    },
    shadow: {
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.2,
        shadowRadius: 2,
        elevation: 5,
        backgroundColor: colors.themeVeryLightGrey,

    },
    headerContainer: {
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center',
        height: TOP_CONTAINER_HEIGHT,
        paddingLeft: 4,
        paddingRight: 8

    }
})