import { RButton } from "components/Buttons/RButton";
import { InputLabel } from "components/InputLabel";
import { ModalCard } from "components/RCard/ModalCard";
import { RKeyboardAvoidingView } from "components/RKeyboardAvoidingView";
import { Spacer } from "components/Spacer";
import { HeaderText } from "components/Typography/HeaderText";
import { RText } from "components/Typography/RText";
import { colors } from "constants/colors";
import { textInputStyle } from "constants/textInputConfig";
import React, { useEffect, useRef, useState } from "react";
import { Animated, Modal, Platform, StyleSheet, TextInput, TouchableWithoutFeedback, View } from "react-native";
import { DateFormat } from "types/date";
import { dateUtil } from "util/dateUtil";
import { dimensionsUtil } from "util/dimensionsUtil";

interface Props {
    heading: string,
    value: Date,
    onChangeDate: (date: Date) => void,
    onCancel: () => void,
    minimumDate?: Date,
    maximumDate?: Date,
    format?: DateFormat
}

enum AnimationState {
    forward,
    reverse,
    default
}

export const DateInputManualEntry = (props: Props) => {
    const textInputRef = useRef(null);
    const [textValue, setTextValue] = useState(null);
    const [error, setError] = useState<string>()
    const [animationState, setAnimationState] = useState(AnimationState.default);
    const animationValue = useRef(new Animated.Value(0)).current
    const format = props.format ? props.format : 'DD/MM/YYYY'


    useEffect(() => {
        setAnimationState(AnimationState.forward);

    }, [])

    useEffect(() => {
        if (animationState === AnimationState.forward) {

            Animated.timing(animationValue, {
                toValue: 1,
                duration: 400,
                useNativeDriver: true,
            }).start(() => {

            });

            //Must be 100 delay for Android
            setTimeout(() => {
                if (textInputRef.current)
                    textInputRef.current.focus();
            }, 100)

        }
    }, [animationState])

    const onSave = () => {
        if (textValue.length !== format.length) {
            setError(`Date must be in the format ${format}`)
            return;
        }
        let date = dateUtil.parseFormInput(textValue, format, props.minimumDate, props.maximumDate);


        if (date) {
            props.onChangeDate(date);
        } else {
            setError("Invalid date")
        }
    }

    const onChangeText = (text: string) => {
        if (text?.length === 2) {
            if (textValue.length === 1) {
                text += "/"
            } else {
                text = text.substring(0, text.length - 1)
            }
        }

        if (text?.length === 5 && format === 'DD/MM/YYYY') {
            if (textValue.length === 4) {
                text += "/"
            } else {
                text = text.substring(0, text.length - 1)
            }
        }

        setTextValue(text);
    }

    let transform;
    let opacity;
    if (Platform.OS === 'android') {
        transform = [{
            translateY: animationValue.interpolate({
                inputRange: [0, 1],
                outputRange: [dimensionsUtil.getScreenHeight(), 0]
            })
        }]
    } else {
        opacity = animationValue;
    }
    const heading = props.heading?.replace(":", "")
    const label = `Date (${format}):`

    return (

        <Modal presentationStyle="overFullScreen" transparent  >
            <TouchableWithoutFeedback onPress={props.onCancel} style={styles.pressable}>

                <Animated.View style={{ ...styles.modalBackground, opacity: animationValue }}  >
                    <RKeyboardAvoidingView
                    >
                        <Animated.View style={{ ...styles.modalBody, transform: transform, opacity: opacity }}>

                            <ModalCard flex>

                                <HeaderText>{heading}</HeaderText>
                                <Spacer />
                                <View style={styles.textContainer}>
                                    <InputLabel label={label} />

                                    <TextInput
                                        placeholderTextColor={colors.placeholder}
                                        blurOnSubmit={true}
                                        ref={textInputRef}
                                        keyboardType='decimal-pad'
                                        placeholder={format}
                                        style={styles.textInput}
                                        value={textValue}
                                        onChangeText={onChangeText} />
                                </View>
                                <Spacer small />
                                <RText color={colors.error}>{error}</RText>


                                <View style={styles.buttonContainer} >
                                    <RButton icon="close" label="Cancel" onPress={props.onCancel} color={colors.default} />
                                    <View style={styles.gap} />
                                    <RButton icon="check" label="OK" color={colors.primary} onPress={onSave} />

                                </View>


                            </ModalCard>
                        </Animated.View>

                    </RKeyboardAvoidingView>

                </Animated.View>
            </TouchableWithoutFeedback>

        </Modal>




    )
}

const styles = StyleSheet.create({
    pressable: {
        flex: 1
    },
    modalBackground: {
        backgroundColor: colors.modalBackground,
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center'
    },
    modalBody: {
        height: 420,
        width: 420,
        paddingHorizontal: 10,
        borderRadius: 4,
        paddingVertical: 20,

    },
    textInput: {
        ...textInputStyle,
        paddingTop: 0,
    },
    textContainer: {
        borderBottomWidth: 1,
        borderBottomColor: colors.themeDarkGrey,
        paddingBottom: 5,
        marginTop: 10
    },
    buttonContainer: {
        flexDirection: 'row',
        flex: 1, alignItems: 'flex-end', justifyContent: 'flex-end', width: '100%'
    },
    gap: {
        width: 20
    }
})
