import { RIconButton } from "components/Buttons/RIconButton";
import { RText } from "components/Typography/RText";
import { RWeakText } from "components/Typography/RWeakText";
import { colors } from "constants/colors";
import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, TouchableOpacity } from "react-native";

interface Props {
    onValueChanged: (value) => void,
    label: string,
    isChecked: boolean,
    marginRight?: boolean
}
export const RCheckBox = (props: Props) => {
    let style;
    if (props.marginRight) {
        style = [styles.container, styles.marginRight]
    } else {
        style = styles.container
    }

    const onChanged = () => {
        props.onValueChanged(!props.isChecked)
    }

    if (!props.isChecked) {
        return (
            <TouchableOpacity style={style} onPress={onChanged} hitSlop={measurementConstants.hitSlop}>
                <RIconButton onPress={onChanged} icon={"checkbox-blank-outline"} size={24} />
                <RWeakText containerStyle={styles.text}>{props.label}</RWeakText>
            </TouchableOpacity>
        )
    } else {
        return (
            <TouchableOpacity style={style} onPress={onChanged}>
                <RIconButton onPress={onChanged} icon={"checkbox-marked"} color={colors.primary} size={24} />
                <RText containerStyle={styles.text}>{props.label}</RText>
            </TouchableOpacity>
        )
    }
}

const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        alignItems: 'center'
    },
    marginRight: {
        marginRight: 8
    },
    text: {
        marginLeft: 4
    }
})
