import { FormTextInputMulti } from "components/FormTextInputMulti";
import { SquashedCard } from "components/RCard/SquashedCard";
import { RScreenContainer } from "components/RScreenContainer";
import React, { useState } from "react";

export const NotesScreen = () => {
    const [recommendations, setRecommendations] = useState(null);
    const [vehicleNotes, setVehicleNotes] = useState(null);

    return (
        <RScreenContainer title="Notes" noPadding  >
            <SquashedCard flex>

                <FormTextInputMulti label={"Recommendations"} value={recommendations}
                    onChangeText={setRecommendations}
                />
                <FormTextInputMulti label={"Vehicle Notes"} value={vehicleNotes}
                    onChangeText={setVehicleNotes}
                />

            </SquashedCard>



        </RScreenContainer>
    );
}
