import { SaveImagePayload } from "types/image";

let _instance: SignatureBuilder;



export class SignatureBuilder {
    private signature: SaveImagePayload
    private signatureBase64: string


    static getInstance() {
        if (!_instance) {
            _instance = new SignatureBuilder();
        }
        return _instance;
    }

    static reset(builder: SignatureBuilder) {
        _instance = builder;
    }


    setSignature(base64: string) {
        const image: SaveImagePayload = {
            description: "Signature",
            sequence: 1,
            view: "signature",
            fullImage: base64
        };
        this.signatureBase64 = base64
        this.signature = image;
    }
    getSignatureBase64() {
        return this.signatureBase64
    }

    getSignaturePayload() {
        return this.signature;
    }



}

