import { DepartmentDataView } from "components/SelectDepartment/DepartmentDataView"
import { SelectDepartmentText } from "components/SelectDepartment/SelectDepartmentText"
import React, { useState } from "react"
import { View } from "react-native"
import { DivisionData } from "types/login"

interface Props {
    data: DivisionData,
    companyId: string
}

export const DivisionDataView = (props: Props) => {
    const [childrenHeight, setChildrenHeight] = useState(0);
    const label = `${props.companyId}.${props.data.id}. ${props.data.name}`

    const onLayout = (event) => {
        var { x, y, width, height } = event.nativeEvent.layout;
        setChildrenHeight(height);
    }

    const isLabelVisible = childrenHeight > 0
    return (
        <View>
            {isLabelVisible && <SelectDepartmentText level={2}>{label}</SelectDepartmentText>}
            <View onLayout={onLayout} >
                {props.data.departmentData.map((item, index) => {
                    return (
                        <DepartmentDataView
                            key={item.id}
                            data={item}
                            companyId={props.companyId}
                            divisionId={props.data.id} />)

                })}
            </View>

        </View>
    )


}