import React from 'react';
import { StyleSheet, View } from 'react-native';
import { IconButton } from 'react-native-paper';
import { useSafeAreaInsets } from 'react-native-safe-area-context';


/**
 * Fixed button in top left
 */
interface Props {
    onPress: () => void
}

export const ChevronLeft = (props: Props) => {

    const insets = useSafeAreaInsets();



    const fixedTopStyle = [styles.fixed, { top: insets.top + 2 }]

    return (

        <View style={fixedTopStyle}>
            <IconButton icon="chevron-left" color='white' size={30} onPress={props.onPress} />
        </View>

    );
}

const styles = StyleSheet.create({

    fixed: {
        position: 'absolute',
        top: 0,
        left: 0
    }
})