import { RLink } from "components/Buttons/RLink";
import { RCard } from "components/RCard";
import { RScreenContainer } from "components/RScreenContainer";
import { SearchInput } from "components/SearchInput";
import { Spacer } from "components/Spacer";
import React, { useEffect } from "react";
import { StyleSheet, View } from "react-native";
import { ScreenName } from "types/navigation";
import { navigationUtil } from "util/navigationUtil";
import { splashUtil } from "util/splashUtil";



export const HomeScreen = () => {


    useEffect(() => {
        splashUtil.hideFakeSplash();
    }, [])

    /**
     * You have to type something to get it to work
     **/
    const onSearch = (text: string) => {
        navigationUtil.navigate(ScreenName.Jobcard, {
            jobcardId: text
        })
    }

    const onPressOverview = () => {
        navigationUtil.navigate(ScreenName.Overview)
    }

    return (
        <RScreenContainer title="Home" noPadding  >
            <RCard flex>


                <View style={styles.container}>
                    <View style={styles.searchContainer}>
                        <SearchInput onSearch={onSearch} autoCapitalize placeholder="Enter rego number" />
                    </View>

                    <Spacer />
                    <RLink label={"Display Overview"} onPress={onPressOverview} />
                </View>

            </RCard>



        </RScreenContainer>
    );
}

const styles = StyleSheet.create({
    container: {
        alignItems: 'center'
    },
    searchContainer: {
        width: 500
    }
})
