import { useRoute } from "@react-navigation/native";
import { Footer } from "components/Footer";
import { LineSeperator } from "components/LIneSeperator";
import { ScrollableCard } from "components/RCard/ScrollableCard";
import { RScreenContainer } from "components/RScreenContainer";
import { Spacer } from "components/Spacer";
import React from "react";
import { ScrollView } from "react-native";
import { AdvisorSection } from "./AdvisorSection";
import { ContactSection } from "./ContactSection";
import { OperationLinesSection } from "./OperationLinesSection";
import { VehicleDetailSection } from "./VehicleDetailSection";
import { WaitJobSection } from "./WaitJobSection";

export interface JobcardScreenParams {
    jobcardId: string
}

export const JobcardScreen = () => {
    const route = useRoute();
    let params = route.params as JobcardScreenParams;
    if (!params) {
        params = {
            jobcardId: "REPLACE ME"
        }
    }

    return (
        <RScreenContainer title="Jobcard" noPadding jobcardId={params.jobcardId}  >
            <ScrollableCard flex>
                <ScrollView>
                    <AdvisorSection />
                    <Spacer small />
                    <LineSeperator />
                    <ContactSection />
                    <VehicleDetailSection />
                    <WaitJobSection />
                    <OperationLinesSection />
                </ScrollView>

            </ScrollableCard>
            <Footer />


        </RScreenContainer>
    );
}

