import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    lastUpdateTime: 0
}

export const slice = createSlice({
    name: 'photos',
    initialState,
    reducers: {
        registerChange: (state) => {
            state.lastUpdateTime = (new Date).valueOf()
        }
    }
})

export const photosActions = slice.actions;
export const photosReducer = slice.reducer
