import { ImageRequestBuilder } from "builders/ImageRequestBuilder"
import { Spacer } from "components/Spacer"
import React, { useEffect, useState } from "react"
import { ScrollView, View } from "react-native"
import { useSelector } from "react-redux"
import { RootState } from "reducers/store"
import { PhotoMap } from "screens/PhotosScreen/PhotoList/PhotoMap"
import { SimpleImageData } from "types/images"
import { PhotoHeaderText } from "./PhotoHeaderText"


interface Props {
    onPressAddPhoto: () => void
}

export const PhotoList = (props: Props) => {
    const lastUpdateTime = useSelector((state: RootState) => state.photos.lastUpdateTime)
    const [photos, setPhotos] = useState<SimpleImageData[]>([]);

    useEffect(() => {
        const builder = ImageRequestBuilder.getInstance();
        const localPhotos = builder.getLocalPhotos();
        setPhotos([...localPhotos]);
    }, [lastUpdateTime])

    if (photos.length === 0) {
        return <View style={{ flex: 1 }}>
            <View style={{ flex: 1 }} />
            <PhotoHeaderText onPress={props.onPressAddPhoto}>Add New Photo</PhotoHeaderText>
            <View style={{ flex: 2 }} />

        </View>
    }

    return (
        <View style={{ width: '100%' }}>
            <ScrollView  >
                <PhotoMap photos={photos} />
                <Spacer />
            </ScrollView>



        </View>


    )



}



