import { RText } from "components/Typography/RText"
import { colors } from "constants/colors"
import React from "react"
import { StyleSheet, TouchableOpacity, View } from "react-native"


interface Props {
    children: string,
    onPress: () => void,
    backgroundColor?: string
}
const _ListText = (props: Props) => {

    const onPress = () => {
        props.onPress()
    }

    return (
        <View style={[styles.row, { backgroundColor: props.backgroundColor }]}>
            <TouchableOpacity style={styles.touchable} onPress={onPress}>
                <RText>{props.children}</RText>

            </TouchableOpacity>
        </View>
    )
}

const propsAreEqual = (prevProps: Props, nextProps: Props) => {
    return true
}

export const ListText = React.memo(_ListText, propsAreEqual)

const styles = StyleSheet.create({
    row: {
        borderBottomWidth: 1,
        borderColor: colors.lineSeperator,
        borderRadius: 4,
    },
    bottomRow: {
        flexDirection: 'row',

    },
    icon: {
        marginRight: 10,
        paddingTop: 1
    },
    touchable: {
        width: '100%',
        paddingBottom: 14,
        paddingTop: 14,
        paddingLeft: 14,
    }

})