const config = {
    appId: 'au.com.rev.testdrive.expo',
    androidFallback: 'https://play.google.com/store/apps/details?id=au.com.dealerdrive&hl=en_AU&gl=US',
    iosFallback: 'https://apps.apple.com/au/app/dealer-drive/id667786452'
}

function createUrl(jobcardId: string) {
    const dynamicLinkPrefix = "https://testdriverev.page.link";
    const appId = config.appId; //"au.com.rev.testdrive.expo";
    const afl = encodeURIComponent(config.androidFallback);
    const ifl = encodeURIComponent(config.iosFallback)
    const deepLink = `https://rev.com.au/testdrive?jobcard=${jobcardId}`;
    const encodedDeepLink = encodeURIComponent(deepLink);
    let dynamicLink = `${dynamicLinkPrefix}/?link=${encodedDeepLink}&apn=${appId}&ibi=${appId}&ifl=${ifl}&afl=${afl}`
    return dynamicLink;
}

export const qrCodeUtil = {
    createUrl: createUrl
}