import { colors } from "constants/colors"
import React from "react"
import { StyleSheet, TouchableOpacity } from "react-native"

interface Props {
    index: number,
    children: any,
    onPress?: () => void
}
export const ShadedLine = (props: Props) => {
    const containerStyle = [styles.row];
    if (props.index % 2 === 0) {
        //@ts-ignore
        containerStyle.push(styles.shadeBackground)
    }


    return (
        <TouchableOpacity style={containerStyle} onPress={props.onPress} disabled={!props.onPress}>
            {props.children}
        </TouchableOpacity>
    )
}



const styles = StyleSheet.create({
    row: {
        borderBottomWidth: 1,
        borderColor: colors.lineSeperator,
        flexDirection: 'row',
        height: 50,
        alignItems: 'center',
    },
    shadeBackground: {
        backgroundColor: colors.themeVeryLightGrey
    }

})