import React from "react";
import { deviceUtil } from "util/deviceUtil";
import { PhotoCardContainerPhone } from "./PhotoCardContainerPhone";
import { PhotoCardContainerTablet } from "./PhotoCardContainerTablet";

interface Props {
    children: any,
    flex?: boolean,
    onPress?: () => void
}

export const PhotoCardContainer = (props: Props) => {

    if (deviceUtil.isTablet()) {
        return <PhotoCardContainerTablet {...props} />
    }


    return (
        <PhotoCardContainerPhone {...props} />
    )
}
