import { OverviewData } from "types/overview";
import { SortDirection } from "types/shared";

function sortByRego(data: OverviewData[], sortDirection: SortDirection) {
    data.sort((a, b) => {
        return getSortResult(a.registration, b.registration, sortDirection)
    })

    return data;
}

function sortByInDate(data: OverviewData[], sortDirection: SortDirection) {
    data.sort((a, b) => {
        return getSortResult(a.inDate, b.inDate, sortDirection)
    })

    return data;
}

function sortByOutDate(data: OverviewData[], sortDirection: SortDirection) {
    data.sort((a, b) => {
        return getSortResult(a.outDate, b.outDate, sortDirection)
    })

    return data;
}

function getSortResult(a, b, sortDirection: SortDirection) {
    const nullOverride = sortDirection === 'up' ? 'ZZZZZ' : ''
    const aFixed = a ? a : nullOverride
    const bFixed = b ? b : nullOverride
    //Less than 0 sort b after a
    if (aFixed === bFixed) return 0;
    let result = aFixed > bFixed ? 1 : -1;
    if (sortDirection === 'up') {
        return result;
    } else {
        return -result;
    }
}

export const overViewUtil = {
    sortByRego: sortByRego,
    sortByInDate: sortByInDate,
    sortByOutDate: sortByOutDate
}