import { SquashedCard } from "components/RCard/SquashedCard";
import { RScreenContainer } from "components/RScreenContainer";
import { Spacer } from "components/Spacer";
import { RText } from "components/Typography/RText";
import React, { useEffect, useState } from "react";
import { StyleSheet, View } from "react-native";
import QRCode from "react-qr-code";
import { qrCodeUtil } from "util/qrCodeUtil";


export const LoanCheckOutScreen = () => {
    const [url, setUrl] = useState(null);

    useEffect(() => {
        const url = qrCodeUtil.createUrl("SJ16071822");
        setUrl(url);

    }, [])

    if (!url) {
        return null;
    }

    return (
        <RScreenContainer title="Loan Vehicle Check Out" noPadding  >
            <SquashedCard flex>
                <View style={styles.container}>
                    <RText>Complete the loan vehicle checkout via the Test Drive app. Scan the QR Code using the camera on your mobile device.</RText>
                    <Spacer />
                    <QRCode value={url} />
                    <Spacer />

                </View>

            </SquashedCard>



        </RScreenContainer>
    );
}

const styles = StyleSheet.create({
    container: {
        alignItems: 'center'
    }
})