import { RIcon } from "components/RIcon"
import { RText } from "components/Typography/RText"
import React from "react"
import { StyleSheet, TouchableOpacity } from "react-native"
import { SortDirection } from "types/shared"



interface Props {
    label: string,
    sortLabel?: string,
    sortDirection?: SortDirection,
    onToggleSort?: (label: string) => void

}
export const OverviewHeaderText = (props: Props) => {

    const onPress = () => {
        props.onToggleSort(props.label);
    }

    const renderLabel = () => {
        if (!props.sortLabel || props.sortLabel !== props.label) {
            return null;
        }

        if (props.sortDirection === 'down') {
            return <RIcon icon="menu-down" color='white' />

        } else {
            return <RIcon icon="menu-up" color='white' />
        }

    }

    const underline = !!props.onToggleSort

    return (
        <TouchableOpacity disabled={!props.onToggleSort} style={styles.container} onPress={onPress}>
            <RText underline={underline} color={'white'} >{props.label}</RText>
            {renderLabel()}
        </TouchableOpacity>

    )
}

const styles = StyleSheet.create({
    container: {
        flexDirection: 'row'
    }
})