import { TextInputLabel } from "components/DollarInput/TextInputLabel";
import { RText } from "components/Typography/RText";
import { colors } from "constants/colors";
import { fontSizes } from "constants/fontSizes";
import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, TextInput, View } from "react-native";
import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

interface Props {
    label: string,
    value: string,
    onChangeText: (text: string) => void,
    /**
     * https://lefkowitz.me/visual-guide-to-react-native-textinput-keyboardtype-options/
     * Use number-pad if decimals not required
     */
    icon?: string
}
export const DollarInput = (props: Props) => {
    const icon = props.icon ? props.icon : null;


    return (
        <View style={styles.container} >
            <TextInputLabel  >{props.label}</TextInputLabel>

            <View style={styles.row}>
                <RText >$</RText>
                <TextInput blurOnSubmit={true} keyboardType='numeric'
                    style={styles.textInput}
                    value={props.value}
                    onChangeText={props.onChangeText}
                    placeholder="0" />
                {props.icon &&
                    <View style={styles.iconContainer}>
                        <MaterialCommunityIcons name={icon} color={colors.themeLightGrey} size={26} />

                    </View>
                }


            </View>

        </View>
    )
}


const styles = StyleSheet.create({
    textInput: {
        fontSize: fontSizes.normal,
        color: colors.black,
        paddingTop: 0,
        flex: 1,
        borderBottomWidth: 1,
        borderBottomColor: colors.themeDarkGrey,
        marginLeft: 20,
        fontFamily: 'RobotoCondensed-Regular',
    },
    container: {
        marginBottom: measurementConstants.formComponentMarginBottom,
        paddingBottom: 5
    },
    row: {
        flexDirection: 'row'
    },
    iconContainer: {
        backgroundColor: 'white',
        justifyContent: 'center',
        alignItems: 'center',
        marginLeft: 10
    }
})