import { SimpleImageData } from "types/images";
import { stringUtil } from "util/stringUtil";

let _instance: ImageRequestBuilder;

export class ImageRequestBuilder {
    isRemoteImagesFetched: boolean

    private remotePhotos: SimpleImageData[];
    private localPhotos: SimpleImageData[];
    private remoteDamage: SimpleImageData;
    private localDamage: SimpleImageData;
    /**
     * These are remote photos that have been marked locally
     * Local marked photos are int he localPhotos property
     */
    private markedPhotos: SimpleImageData[]

    private constructor() {
        this.remotePhotos = [];
        this.markedPhotos = [];
        this.localPhotos = [];
    }

    static getInstance() {
        if (!_instance) {
            _instance = new ImageRequestBuilder();
        }

        return _instance;
    }

    static reset() {
        _instance = new ImageRequestBuilder();
    }

    hasPhotos() {
        return this.remotePhotos?.length > 0 || this.localPhotos?.length > 0
    }

    getEmailAttachments() {
        const result: string[] = [];
        if (this.localDamage?.markedFilePath) {
            result.push(this.localDamage.markedFilePath)
        }
        if (this.localPhotos) {
            this.localPhotos.forEach(x => {
                const filePath = x.markedFilePath ? x.markedFilePath : x.filePath
                result.push(filePath);
            })
        }
        return result;

    }

    deletePhoto(id: string) {
        this.localPhotos = this.localPhotos.filter(x => x.id !== id);
    }

    addLocalPhoto(filePath: string) {
        const simpleImage: SimpleImageData = {
            filePath: filePath,
            x_rowid: null,
            isLocal: true,
            id: stringUtil.createRandomGuid()
        }

        this.localPhotos.push(simpleImage)
    }

    hasRequestedFromServer() {
        return this.isRemoteImagesFetched;
    }

    /**
     * 
     * @returns Merge of remote photos and local marked photos
     */
    getRemotePhotos() {
        let mergedRemotePhotos = [];
        for (let i = 0; i < this.remotePhotos.length; i++) {
            const photo = this.remotePhotos[i];
            console.log("remotePhotos forEach", photo)

            const markedPhoto = this.getMarkedPhotoByServerId(photo.x_rowid);

            if (markedPhoto) {
                mergedRemotePhotos.push(markedPhoto);
                console.log("pushing remote photo")

            }
            else {
                mergedRemotePhotos.push(photo);
                console.log("pushing remote photo")
            }
        }


        console.log("getRemotePhotos AAAA", this.remotePhotos.length, mergedRemotePhotos)

        return mergedRemotePhotos;
    }

    getLocalPhotos() {
        return this.localPhotos;
    }

    private getMarkedPhotoByServerId(x_rowid: string) {
        if (!this.markedPhotos) {
            return null;
        }
        return this.markedPhotos.find(x => x.x_rowid === x_rowid);
    }

    addMarkedPhoto(originalImage: SimpleImageData, newFilePath: string, paths: any[]) {
        console.log("addMarkedPhoto", originalImage, newFilePath)

        if (originalImage.x_rowid) {
            const newMarkedPhotos = this.markedPhotos.filter(x => x.id !== originalImage.id);
            newMarkedPhotos.push({
                ...originalImage,
                id: stringUtil.createRandomGuid(),
                isLocal: true,
                markedFilePath: newFilePath,
                markings: paths
            })
            this.markedPhotos = newMarkedPhotos;
        }
        else {
            const found = this.localPhotos.find(x => x.id === originalImage.id);
            found.markedFilePath = newFilePath;
            found.markings = paths;
            console.log("addMarkedPhoto", found)

        }


    }

    /**
     * Butterfly stuff
     */
    getButterflyMarkings() {
        return this.localDamage?.markings;
    }

    updateButterfly(markings: any[], newFilePath: string) {
        /* this.localDamage = {
             ...this.remoteDamage,
             markedFilePath: newFilePath,
             markings: markings,
             id: stringUtil.createRandomGuid(),
             isLocal: true
         }*/
    }

}

