import { Spacer } from "components/Spacer";
import React, { useEffect, useState } from "react";
import { StyleSheet, View, useWindowDimensions } from "react-native";
import { ReactSketchCanvas } from 'react-sketch-canvas';

interface Props {
    backgroundImage: string
}

const PIXEL = "https://i.imgur.com/3AmvBrv.png";

export const SketchCanvas = (props: Props) => {
    const [backgroundImage, setBackgroundImage] = useState(PIXEL);
    const [width, setWidth] = useState(null);
    const dimensions = useWindowDimensions();
    useEffect(() => {
        if (props.backgroundImage) {
            setBackgroundImage(PIXEL);
            setTimeout(() => {
                setBackgroundImage(props.backgroundImage);
            }, 200)
        }

    }, [props.backgroundImage, dimensions.width])

    useEffect(() => {
        if (!!width) {
            return;
        }
        const height = dimensions.height;
        const imageWidth = height * 600 / 1000
        setWidth(imageWidth)

    }, [dimensions.height])

    if (!props.backgroundImage || !width) {
        return null;
    }
    return (
        <View style={styles.container}>
            <View style={{ flex: 1, width: width }}>
                <ReactSketchCanvas

                    strokeWidth={4}
                    strokeColor="red"
                    backgroundImage={backgroundImage}
                />
            </View>
            <Spacer small />
        </View>


    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        alignItems: 'center'
    }
})