import { FormTextInput } from "components/FormTextInput";
import { RCheckBox } from "components/RCheckBox";
import { Spacer } from "components/Spacer";
import React from "react";
import { StyleSheet, View } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { editCustomerActions } from "reducers/editCustomer";
import { RootState } from "reducers/store";
import { ContactMethod } from "types/editCustomer";

interface Props {
    declinable?: boolean,
    type: ContactMethod
}

export const DeclinableNumber = (props: Props) => {

    const label = props.type === 'Email' ? "Email Address" : `${props.type} Number`
    const declineLabel = `${props.type} Declined?`
    const keyboardType = props.type === 'Email' ? 'email-address' : 'phone-pad'
    const dispatch = useDispatch();
    const declinedMethods = useSelector((state: RootState) => state.editCustomer.declinedMethods);
    const isDeclined = declinedMethods.indexOf(props.type) > -1



    const setIsChecked = (value: boolean) => {
        if (value) {
            if (props.type === ContactMethod.Mobile) {
                dispatch(editCustomerActions.addDeclinedMethod(ContactMethod.Mobile))
                dispatch(editCustomerActions.addDeclinedMethod(ContactMethod.SMS))
            } else {
                dispatch(editCustomerActions.addDeclinedMethod(props.type))
            }

        } else {
            if (props.type === ContactMethod.Mobile) {
                dispatch(editCustomerActions.removeDeclinedMethod(ContactMethod.Mobile))
                dispatch(editCustomerActions.removeDeclinedMethod(ContactMethod.SMS))
            } else {
                dispatch(editCustomerActions.removeDeclinedMethod(props.type))
            }
        }
    }


    return (
        <View>
            <View style={styles.container}>
                <View style={{ flex: 1 }}>
                    <FormTextInput label={label} value={""} onChangeText={function (text: string): void {
                        throw new Error("Function not implemented.");
                    }} keyboardType={keyboardType} />
                </View>
                <View style={styles.rightColumn}>
                    {props.declinable &&
                        <RCheckBox onValueChanged={setIsChecked} label={declineLabel} isChecked={isDeclined} />
                    }

                </View>

            </View>
            <Spacer small />

        </View>





    );
}

const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        alignItems: 'center'
    },
    rightColumn: {
        width: 200
    }
})