import { RIcon } from "components/RIcon";
import { RText } from "components/Typography/RText";
import { colors } from "constants/colors";
import { fontSizes } from "constants/fontSizes";
import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
/**
 * This button will fill up space
 */
interface Props {
    onPress: () => void,
    label: string,
    icon: string
    type: 'success' | 'primary' | 'default',
    containerStyle?: any,
    showCloseIcon?: boolean
}
export const SmallButton = (props: Props) => {
    let customColor = colors[props.type]
    const containerStyle = [styles.container, { borderColor: customColor }, props.containerStyle]
    return (
        <TouchableOpacity onPress={props.onPress}>
            <View style={containerStyle}>
                <RIcon icon={props.icon} color={customColor} size={fontSizes.small} />
                <RText containerStyle={styles.text} small color={customColor}>{props.label.toUpperCase()}</RText>
                {props.showCloseIcon && <RIcon icon="close" color={customColor} size={fontSizes.small} />}
            </View>
        </TouchableOpacity>

    )
}


const styles = StyleSheet.create({
    container: {
        flexDirection: 'row',
        backgroundColor: 'white',
        borderWidth: 2,
        borderRadius: 4,
        alignItems: 'center',
        paddingHorizontal: 6,
        height: measurementConstants.smallButtonHeight
    },
    text: {
        marginHorizontal: 6
    }
})