import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ContactMethod } from 'types/editCustomer';
import { arrayUtil } from 'util/arrayUtil';

interface State {
    selectedSection: number,
    preferredContact: ContactMethod,
    allowedContactMethods: ContactMethod[],
    declinedMethods: ContactMethod[]
}

const initialState: State = {
    selectedSection: -1,
    preferredContact: null,
    allowedContactMethods: [],
    declinedMethods: []
}


function removeAllowedContactMethod(state: State, contactMethod: ContactMethod) {
    state.allowedContactMethods = arrayUtil.remove(contactMethod, state.allowedContactMethods);
    if (contactMethod === state.preferredContact) {
        if (state.allowedContactMethods.length > 0) {
            const firstItem = state.allowedContactMethods[0];
            state.preferredContact = firstItem;
        } else {
            state.preferredContact = null;
        }
    }
}

export const slice = createSlice({
    name: 'editCustomer',
    initialState,
    reducers: {
        setSelectedSection: (state, action: PayloadAction<number>) => {
            state.selectedSection = action.payload
        },
        updatePreferredContact: (state, action: PayloadAction<ContactMethod>) => {
            state.preferredContact = action.payload;
            if (state.preferredContact) {
                state.allowedContactMethods = arrayUtil.addIfNotExists(action.payload, state.allowedContactMethods);
            }
        },
        addAllowedContactMethod: (state, action: PayloadAction<ContactMethod>) => {
            state.allowedContactMethods = arrayUtil.addIfNotExists(action.payload, state.allowedContactMethods);
        },
        removeAllowedContactMethod: (state, action: PayloadAction<ContactMethod>) => {
            removeAllowedContactMethod(state, action.payload);
        },
        addDeclinedMethod: (state, action: PayloadAction<ContactMethod>) => {
            state.declinedMethods = arrayUtil.addIfNotExists(action.payload, state.declinedMethods);
            removeAllowedContactMethod(state, action.payload);
        },
        removeDeclinedMethod: (state, action: PayloadAction<ContactMethod>) => {
            state.declinedMethods = arrayUtil.remove(action.payload, state.declinedMethods);
        },
        reset: (state) => {
            return initialState
        },
    }
})

export const editCustomerActions = slice.actions;
export const editCustomerReducer = slice.reducer
