import { createSlice } from '@reduxjs/toolkit';
import { SelectDepartmentState } from 'types/selectDepartment';

const initialState: SelectDepartmentState = {
    filterText: '',
    isNextButtonVisible: false
}



export const selectDepartmentSlice = createSlice({
    name: 'selectDepartment',
    initialState,
    reducers: {

        setFilterText: (state, action) => {
            state.filterText = action.payload

        },

        setIsNextButtonVisible: (state, action) => {
            state.isNextButtonVisible = action.payload
        },
        resetDepartment: (state) => {
            return initialState
        }

    }
})

export const { setFilterText, setIsNextButtonVisible, resetDepartment } = selectDepartmentSlice.actions;
export const selectDepartmentReducer = selectDepartmentSlice.reducer
