import { RTextInput } from "components/RTextInput";
import { Spacer } from "components/Spacer";
import React from "react";


interface Props {
    label: string,
    value: string,
    multiline?: boolean,
    onChangeText: (text: string) => void,
    small?: boolean,
    masked?: boolean,
    /**
     * Number-pad for non decimals; otherwise use decimal-pad; leave blank for default
     */
    keyboardType?: 'number-pad' | 'decimal-pad' | 'phone-pad' | 'email-address',
}
export const FormTextInput = (props: Props) => {
    return (
        <React.Fragment>
            <RTextInput {...props} />
            <Spacer />
        </React.Fragment>
    )

}
