import { measurementConstants } from "constants/measurementConstants";
import React from "react";
import { StyleSheet, TouchableWithoutFeedback, View } from "react-native";

interface Props {
    children: any,
    flex?: boolean,
    noMargin?: boolean
}

/**
 * Allows the user to touch a card without dismissing the modal
 */
export const ModalCard = (props: Props) => {
    const innerStyle = [styles.shadow];
    const outerStyle = [styles.wrapper]
    const touchableStyle = [styles.touchable]
    if (props.flex) {
        //@ts-ignore
        innerStyle.push({ flex: 1 })
        //@ts-ignore
        outerStyle.push({ flex: 1 })
        //@ts-ignore
        touchableStyle.push({ flex: 1 })
    }

    if (props.noMargin) {
        //@ts-ignore
        outerStyle.push(styles.noMargin)
    }


    return (
        //onStartShouldSetResponder is used to enable scroll when FlatList is inside RScreenContainer
        <TouchableWithoutFeedback style={outerStyle} onPress={() => { }}  >
            <View style={[innerStyle, touchableStyle]}  >
                {props.children}
            </View>
        </TouchableWithoutFeedback>
    )


}


const styles = StyleSheet.create({
    wrapper: {
        borderRadius: 16,
        overflow: 'hidden',
    },
    touchable: {
        padding: 18,
    },
    shadow: {
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.2,
        shadowRadius: 2,
        elevation: 3,
        backgroundColor: 'white',
        borderRadius: 6,
        marginVertical: 6,
        marginHorizontal: measurementConstants.cardHorizontalMargin,
    },
    noMargin: {
        marginHorizontal: -measurementConstants.cardHorizontalMargin
    }

})