import { Spacer } from "components/Spacer"
import React from "react"
import { StyleSheet, View } from "react-native"
import { PhotoCard } from "screens/PhotosScreen/PhotoList/PhotoCard"
import { SimpleImageData } from "types/images"
import { deviceUtil } from "util/deviceUtil"

interface Props {
    photos: SimpleImageData[],
}

export const PhotoMap = (props: Props) => {
    if (!props.photos || props.photos.length === 0) {
        return null;
    }

    const listContainerStyle = deviceUtil.isTablet() ? styles.tabletList : styles.phoneList


    return (
        <React.Fragment>

            <View style={listContainerStyle}>
                {props.photos.map((item: SimpleImageData, index: number) => {
                    return (
                        <PhotoCard data={item} key={item.id + item.markedFilePath} />
                    )
                })
                }
            </View>
            <Spacer />

        </React.Fragment>
    )
}

const styles = StyleSheet.create({
    tabletList: {
        flexDirection: 'row', flexWrap: 'wrap'
    },
    phoneList: {
        alignItems: 'center'
    },
    heading: {
        marginLeft: 20
    }
})
