import { ImageRequestBuilder } from 'builders/ImageRequestBuilder';
import { BottomInset } from 'components/BottomInset';
import { FloatingButton } from 'components/Buttons/FloatingButton';
import { RScreenContainer } from 'components/RScreenContainer';
import { SketchCanvas } from 'components/SketchCanvas';
import React, { useEffect, useRef, useState } from "react";
import { StyleSheet, View } from 'react-native';
import { BackgroundImageType, sketchBackgroundService } from 'services/sketchBackgroundService';

export const ButterflyScreen = ({ navigation }) => {
    const [backgroundImage, setBackgroundImage] = useState<string>()
    const [backgroundImageType, setBackgroundImageType] = useState<BackgroundImageType>(BackgroundImageType.car)
    const sketchRef = useRef(null);
    const [visible, setVisible] = React.useState(false);

    useEffect(() => {
        setup(backgroundImageType);
    }, [backgroundImageType])

    /**
    * Restore saved paths
    */
    useEffect(() => {
        const savedPaths = ImageRequestBuilder.getInstance().getButterflyMarkings();
        if (sketchRef.current && savedPaths) {
            //Need to give time for image to render before applying strokes
            setTimeout(() => {
                for (let i = 0; i < savedPaths.length; i++) {
                    const path = savedPaths[i];
                    sketchRef.current.addPath(path);
                }
            }, 100)

        }

    }, [])

    /**
     * Apply effect on leave
     */
    useEffect(() => {
        const unsubscribe = navigation.addListener('beforeRemove', () => {
            // do something
        });

        return unsubscribe;
    }, [navigation]);


    const setup = async (imageType: BackgroundImageType) => {
        const filePath = await sketchBackgroundService.getBackgroundImagePath(imageType)
        setBackgroundImage(filePath);
    }

    const openMenu = () => setVisible(true);

    const closeMenu = () => setVisible(false);

    const onClear = () => {
        closeMenu();
        sketchRef.current.clear()
    }

    const changeBackgroundImage = () => {
        closeMenu();
        if (backgroundImageType === BackgroundImageType.car) {
            setBackgroundImageType(BackgroundImageType.bike)
        } else {
            setBackgroundImageType(BackgroundImageType.car)
        }
    }



    return (
        <RScreenContainer noPadding title='Butterfly Diagram'  >
            <SketchCanvas backgroundImage={backgroundImage} />


            <View style={styles.fixedBottom}>
                <FloatingButton onPress={function (): void {
                    throw new Error('Function not implemented.');
                }} icon={'menu'} />
            </View>
            <BottomInset />
        </RScreenContainer>

    );
}

const styles = StyleSheet.create({
    bottomContainer: {
        width: '100%',
    },
    fixedBottom: {
        position: 'absolute',
        bottom: 20,
        right: 20
    },
    menu: {
        transform: [{ translateY: -20 }]
    },
    center: {
        justifyContent: 'center',
        alignItems: 'center',
        flex: 1
    }
})
