import * as FileSystem from 'expo-file-system';
import { stringUtil } from './stringUtil';



/**
 * Use this to create a file after getting base64 from Sketch Canvas
 * @param base64 This does not have data:image/png;base64
 * @returns 
 */
async function createFileWithExpo(base64: string, fileName: string, usePermanentDir: boolean) {

    const dir = FileSystem.cacheDirectory;
    const uri = dir + "/" + fileName
    const fixedString = fixBase64String(base64)
    await FileSystem.writeAsStringAsync(uri, fixedString, {
        encoding: FileSystem.EncodingType.Base64
    })

    //For some reason we need to do this or it won't work in image and sketch canvas
    return (uri);
}



/**
 * Use this when you want to create an image AFTER using Sketch Canvas
 */
async function createImageWithExpo(base64: string, usePermanentDir: boolean) {
    const fileName = stringUtil.createRandomGuid() + ".jpg";
    return createFileWithExpo(base64, fileName, usePermanentDir);
}


function fixBase64String(base64String: string) {
    if (base64String.startsWith("data:image")) {
        const regex = /data:image\/(jpeg|png);base64,[ ]?/
        return base64String.replace(regex, "")

    }


    return base64String;
}

async function getFileSize(filePath: string) {
    const info = await FileSystem.getInfoAsync(filePath);
    //@ts-ignore
    const sizeBytes = info.size;
    return humanFileSize(sizeBytes)
}

/**
 * Format bytes as human-readable text.
 * 
 * @param bytes Number of bytes.
 * @param si True to use metric (SI) units, aka powers of 1000. False to use 
 *           binary (IEC), aka powers of 1024.
 * @param dp Number of decimal places to display.
 * 
 * @return Formatted string.
 */
function humanFileSize(bytes, si = true, dp = 1) {
    const thresh = si ? 1000 : 1024;

    if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
    }

    const units = si
        ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
        : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
    let u = -1;
    const r = 10 ** dp;

    do {
        bytes /= thresh;
        ++u;
    } while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);


    return bytes.toFixed(dp) + ' ' + units[u];
}

async function deleteFile(filePath: string) {
    try {
        await FileSystem.deleteAsync(filePath);
        console.log(filePath + " deleted")
    } catch (err) {
        console.log("deleteFile err", err)
    }
}

async function logDocumentDir() {
    const info = await FileSystem.readDirectoryAsync(FileSystem.documentDirectory);
    console.log("logDocumentDir", info);
}

/**
 * 
 */
async function writeJsonToFile(object: any) {
    const dir = FileSystem.cacheDirectory;
    const fileName = "temp.txt"
    const uri = dir + "/" + fileName
    const contents = JSON.stringify(object);
    await FileSystem.writeAsStringAsync(uri, contents, {
        encoding: FileSystem.EncodingType.UTF8
    })
    return uri;
}

function fixFilePathForSketchCanvas(filePath: string) {
    if (!filePath) {
        return filePath
    }

    return filePath.replace("file://", "")
}

export const fileUtil = {
    createImageWithExpo: createImageWithExpo,
    getFileSize: getFileSize,
    deleteFile: deleteFile,
    logDocumentDir: logDocumentDir,
    writeJsonToFile: writeJsonToFile,
    fixFilePathForSketchCanvas: fixFilePathForSketchCanvas
}