import { useRoute } from '@react-navigation/native';
import { ChevronLeft } from 'components/Buttons/ChevronLeft';
import React, { useEffect, useRef, useState } from "react";
import { ActivityIndicator, StyleSheet, View } from "react-native";
import { SafeAreaView } from 'react-native-safe-area-context';
import { useDispatch } from 'react-redux';
import { SimpleImageData } from 'types/images';
import { fileUtil } from 'util/fileUtil';
import { navigationUtil } from 'util/navigationUtil';

export const MarkScreen = () => {
    const [isLoading, setIsLoading] = useState(true);
    const [filePath, setFilePath] = useState<string>();
    const route = useRoute();
    const sketchRef = useRef(null);
    const dispatch = useDispatch();
    const params: SimpleImageData = route.params as SimpleImageData
    if (!params) {
        throw 'MarkScreen route params cannot be null'
    }

    useEffect(() => {
        setup();
    }, [])

    const setup = async () => {
        const fPath = params.markedFilePath ? params.markedFilePath : params.filePath
        if (!fPath) {
            throw "Filepath cannot be empty"
        }
        //We need to copy the expo image to rnfetch blob to get this to work
        setFilePath(fileUtil.fixFilePathForSketchCanvas(fPath));
        setIsLoading(false);
    }

    const onPressBack = () => {
        save();
    }

    const save = () => {
        navigationUtil.back();



    }


    if (isLoading) {
        return (
            <SafeAreaView style={styles.loadingContainer}  >
                <ActivityIndicator animating />
            </SafeAreaView>
        )
    }
    return (
        <SafeAreaView style={styles.container}  >

            <View style={styles.iconContainer}>
                <ChevronLeft onPress={onPressBack} />
            </View>



        </SafeAreaView>
    );
};

const styles = StyleSheet.create({

    container: {
        flex: 1,
        backgroundColor: 'black',
    },
    loadingContainer: {
        flex: 1,
        backgroundColor: 'black',
        justifyContent: 'center',
        alignItems: 'center'
    },
    iconContainer: {
        position: 'absolute',
        top: 0,
        left: 0
    }
});

