import React from "react";
import { Pressable, StyleSheet, View } from "react-native";

interface Props {
    children: any,
    flex?: boolean,
    onPress?: () => void
}

export const PhotoCardContainerPhone = (props: Props) => {
    const innerStyle = [styles.shadow];
    const outerStyle = [styles.wrapper]
    if (props.flex) {
        //@ts-ignore
        innerStyle.push({ flex: 1 })
        //@ts-ignore
        outerStyle.push({ flex: 1 })
    }

    const onPress = () => {
        if (props.onPress) {
            props.onPress()
        }
    }


    return (
        //onStartShouldSetResponder is used to enable scroll when FlatList is inside RScreenContainer
        <View style={outerStyle} onStartShouldSetResponder={() => true}>
            <View style={innerStyle}  >
                <Pressable onPress={onPress} style={styles.touchable} disabled={!props.onPress}>
                    {props.children}
                </Pressable>

            </View>

        </View>



    )
}


const styles = StyleSheet.create({
    wrapper: {
        borderRadius: 16,
        overflow: 'hidden',
        width: 360
    },
    touchable: {
        flex: 1
    },
    shadow: {
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.2,
        shadowRadius: 2,
        elevation: 3,
        backgroundColor: 'white',
        borderRadius: 6,
        marginVertical: 4,
        marginHorizontal: 10,
        height: 300
    },

})