import { LoginResponseDataView } from "components/SelectDepartment/LoginResponseDataView";
import React, { useEffect } from "react";
import { LoginResponse } from "types/login";
import { splashUtil } from "util/splashUtil";
import { RScreenContainer } from "../components/RScreenContainer";


export const SelectDepartmentScreen = ({ route }) => {
    const loginResponse: LoginResponse = route.params;

    useEffect(() => {
        splashUtil.hideFakeSplash();
    }, [])

    return (<RScreenContainer title="Select Department"  >
        <LoginResponseDataView data={loginResponse.responseData} />
    </RScreenContainer >)
}
