import { NumberedSectionBase } from "components/NumberedSection/NumberedSectionBase"
import React, { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { editCustomerActions } from "reducers/editCustomer"
import { RootState } from "reducers/store"

interface Props {
    title: string,
    number: number,
    children: any,
    onPress?: () => void
}
export const NumberedSectionEditCustomer = (props: Props) => {
    const selectedSection = useSelector((state: RootState) => state.editCustomer.selectedSection);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(editCustomerActions.setSelectedSection(1))
    }, [])

    const onPress = () => {
        dispatch(editCustomerActions.setSelectedSection(props.number))
        if (props.onPress) {
            props.onPress();
        }

    }


    return <NumberedSectionBase {...props} onPress={onPress} selectedSection={selectedSection} />
}
