import { colors } from "constants/colors";
import { fontSizes } from "constants/fontSizes";
import React from "react";
import { StyleSheet, TextInput, View } from "react-native";
import { IconButton } from "react-native-paper";
import { ScreenName } from "types/navigation";
import { navigationUtil } from "util/navigationUtil";

interface Props {
    jobcardId?: string
}
export const JobcardInput = (props: Props) => {

    const onPressIcon = () => {
        navigationUtil.resetTo(ScreenName.Home)
    }

    if (!props.jobcardId) {
        return null;
    }

    return (
        <View style={styles.container} >

            <View>
                <TextInput blurOnSubmit={true}
                    value={props.jobcardId}
                    style={styles.textInput}
                    placeholderTextColor={colors.placeholder}
                    onSubmitEditing={onPressIcon}
                />

                <View style={styles.textInputOverlay}>
                    <IconButton icon="close" onPress={onPressIcon} color={colors.themeDarkGrey} />
                </View>

            </View>

        </View>)
}

const styles = StyleSheet.create({
    textInput: {
        fontSize: fontSizes.small,
        fontFamily: 'RobotoCondensed-Regular',
        color: colors.black,
        paddingLeft: 6,
        paddingVertical: 6,
        borderWidth: 1,
        borderColor: colors.themeLightGrey,
        borderRadius: 5,
        backgroundColor: 'white',
    },
    container: {
        marginLeft: 20,
        width: 120,
        paddingVertical: 4
    },
    label: {
        fontFamily: 'RobotoCondensed-Light',
        paddingBottom: 10
    },
    textInputOverlay: {
        position: 'absolute',
        right: -10,
        top: 0,
        bottom: 0,
        justifyContent: 'center'
    }
})