function addIfNotExists(object, arr: any[]) {
    if (arr.indexOf(object) === -1) {
        //Add
        arr.push(object);
    }

    return arr;
}

function remove(object, arr: any) {
    const index = arr.indexOf(object);
    if (index > -1) { // only splice array when item is found
        arr.splice(index, 1); // 2nd parameter means remove one item only
    }

    return arr;
}


export const arrayUtil = {
    addIfNotExists: addIfNotExists,
    remove: remove
}