import { SelectDepartmentText } from "components/SelectDepartment/SelectDepartmentText"
import React, { useState } from "react"
import { View } from "react-native"
import { CompanyData } from "types/login"
import { DivisionDataView } from "./DivisionDataView"

interface Props {
    data: CompanyData
}

export const CompanyDataView = (props: Props) => {
    const [childrenHeight, setChildrenHeight] = useState(0);
    const label = `${props.data.id}. ${props.data.name}`
    const onLayout = (event) => {
        var { x, y, width, height } = event.nativeEvent.layout;
        setChildrenHeight(height);
    }
    const isLabelVisible = childrenHeight > 0
    return (
        <View>
            {isLabelVisible && <SelectDepartmentText level={1}>{label}</SelectDepartmentText>}
            <View onLayout={onLayout} >
                {props.data.divisionData.map((item, index) => {
                    return (
                        <DivisionDataView key={item.id} data={item} companyId={props.data.id} />)

                })}
            </View>

        </View>
    )
}