import { SelectDepartmentText } from "components/SelectDepartment/SelectDepartmentText"
import React from "react"
import { View } from "react-native"
import { useDispatch, useSelector } from "react-redux"
import { setFilterText, setIsNextButtonVisible } from "reducers/selectDepartment"
import { RootState } from "reducers/store"
import { DepartmentData } from "types/login"
import { saveUtil } from "util/saveUtil"
interface Props {
    data: DepartmentData,
    divisionId: string,
    companyId: string
}

export const DepartmentDataView = (props: Props) => {
    const filterText = useSelector((state: RootState) => state.selectDepartment.filterText)
    const label = `${props.companyId}.${props.divisionId}.${props.data.id}. ${props.data.name}`
    const dispatch = useDispatch();

    let isVisible = true;

    const onPress = () => {
        saveUtil.setDepartmentParameters(props.companyId, props.divisionId, props.data.id)
        dispatch(setFilterText(label))
        dispatch(setIsNextButtonVisible(true));
    }

    if (filterText.length > 0) {
        //Apply the filter
        if (label.toLowerCase().indexOf(filterText.toLowerCase()) < 0) {
            isVisible = false;
        }
    }

    if (isVisible) {
        return (
            <View>
                <SelectDepartmentText level={3} onPress={onPress}>{label}</SelectDepartmentText>

            </View>
        )
    } else {
        return null
    }



}