import { bikeImage } from "src/sketchBackgrounds/bikeImage";
import { carImage } from "src/sketchBackgrounds/carImage";

export enum BackgroundImageType {
    car,
    bike
}

let cache = {
    carImageFilePath: null,
    bikeImageFilePath: null
}

async function getCarImageFilePath() {


    return carImage;
}

async function getBikeImageFilePath() {


    return bikeImage;
}

async function getBackgroundImagePath(type: BackgroundImageType) {
    if (type === BackgroundImageType.car) {
        return getCarImageFilePath();
    } else {
        return getBikeImageFilePath();
    }
}

export const sketchBackgroundService = {
    getBackgroundImagePath: getBackgroundImagePath
}