import { RIconButton } from "components/Buttons/RIconButton";
import { colors } from "constants/colors";
import { fontSizes } from "constants/fontSizes";
import React from "react";
import { StyleSheet, Text, TextInput, View } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { setFilterText, setIsNextButtonVisible } from "reducers/selectDepartment";
import { RootState } from "reducers/store";

interface Props {

}
export const SelectDepartmentInput = (props: Props) => {
    const filterText = useSelector((state: RootState) => state.selectDepartment.filterText);
    const isNextButtonVisible = useSelector((state: RootState) => state.selectDepartment.isNextButtonVisible)
    const dispatch = useDispatch();

    const onChangeText = (text) => {
        dispatch(setFilterText(text))
    }

    const onClearText = () => {
        dispatch(setFilterText(''))
        dispatch(setIsNextButtonVisible(false))

    }

    return (
        <View style={styles.container} >
            <Text style={styles.label}>Department:</Text>
            <View>
                <TextInput blurOnSubmit={true}
                    value={filterText}
                    onChangeText={onChangeText}
                    style={styles.textInput}
                    keyboardType='ascii-capable'
                />
                {isNextButtonVisible &&
                    <View style={styles.textInputOverlay}>
                        <RIconButton icon="close" onPress={onClearText} />
                    </View>
                }
            </View>

        </View>)
}

const styles = StyleSheet.create({
    textInput: {
        fontSize: fontSizes.small,
        fontFamily: 'RobotoCondensed-Bold',
        color: colors.black,
        paddingLeft: 6,
        paddingVertical: 12,
        borderWidth: 1,
        borderColor: colors.themeLightGrey,
        borderRadius: 5
    },
    container: {



    },
    label: {
        fontFamily: 'RobotoCondensed-Light',
        paddingBottom: 10
    },
    textInputOverlay: {
        position: 'absolute',
        right: 0,
        top: 0,
        bottom: 0,
        justifyContent: 'center',
    }
})