import uuid from 'react-native-uuid';


function createRandomGuid() {
    return uuid.v4().toString()
}

function formatMoney(inputString: string | number, decimalPlaces?: number) {
    let number = 0;
    if (typeof (inputString) === 'string') {
        number = Number.parseFloat(inputString);
        if (isNaN(number)) {
            return inputString;
        }
    } else {
        number = inputString;
    }

    const numDecimals = decimalPlaces ? decimalPlaces : 2

    return number.toLocaleString('en', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: numDecimals
    });

}

export const stringUtil = {
    formatMoney: formatMoney,
    createRandomGuid: createRandomGuid
}