import { RIcon } from "components/RIcon";
import { colors } from "constants/colors";
import React from "react";
import { StyleSheet, View } from "react-native";

interface Props {
    isTicked: boolean,
}
export const RightColumn = (props: Props) => {

    const renderIcon = () => {
        if (props.isTicked) {
            return <RIcon icon={"check-bold"} color={colors.success} />
        } else {
            return <RIcon icon={"close"} color={colors.error} />
        }
    }


    return (
        <View style={styles.rightColumn}>

            <View style={styles.component}>
                {renderIcon()}
            </View>

        </View>
    )
}


const styles = StyleSheet.create({
    rightColumn: {
        width: 120,
        flexDirection: 'row',
        alignItems: 'center',
        marginLeft: 10
    },
    component: {
        flex: 1,
        alignItems: 'center'
    }

})